.form-control {
	border-radius: 0;
}

textarea.form-control {
	height: 100px;
	resize: none;
}

.checkbox-custom {
	input[type="checkbox"] {
		display: none;
	}

	label,
	span {
		display: block;
	}

	span {
		position: relative;
		background-color: #f6f6f6;
		border-left: 4px solid #d1d1d1;
		font-size: 20px;
		padding: 15px 30px;
		cursor: pointer;
		font-family: 'Gilroy', sans-serif;
		font-weight: 900;
		transition: background-color .2s, border-color .2s;

		&:after {
			content: "\f00c";
			font: normal normal normal 20px/1 FontAwesome;
			position: absolute;
			top: 50%;
			right: 20px;
			transform: translateY(-50%);
			opacity: 0;
			visibility: hidden;
			transition: opacity .2s, visibility .2s;
		}
	}

	input:checked + span {
		border-color: #000;
		background-color: #f5d02c;
		box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);

		&:after {
			opacity: 1;
			visibility: visible;
		}
	}
}

.select-wrapper {
	position: relative;

	select {
		-webkit-appearance: none;
		-ms-progress-appearance: none;
		-moz-appearance: none;
		border: none;
		background-color: #f6f6f6;

		&:focus {
			background-color: #f6f6f6;
			box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
			transition: box-shadow .15s;
		}
	}

	&:after {
		content: "\f0d7";
		font: normal normal normal 14px/1 FontAwesome;
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);
		pointer-events: none;
	}

	&.-transparent {
		select {
			background-color: transparent;
			padding-right: 35px;

			&:focus {
				outline: none;
			}
		}
	}
}

.form-contact {
	background-color: #fff;
	padding: 60px 45px;
	color: #000;
	&.-modal_pricing{
		padding: 0;
	}
	&.-bg_none{
		background: none;
		padding: 0;
	}

	&.-mb {
		margin-bottom: 20px;
	}
}

.form-contact {
	.form-control {
		background-color: #f6f6f6;
		border-color: transparent;
		&:focus{
			background-color: #f6f6f6;
			box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
			transition: box-shadow .15s;
		}
	}
}

.form-search {
	background-color: #f1f1f1;

	.form-control {
		background-color: transparent;
		border-color: transparent;
	}

	.btn {
		background-color: transparent;
		border-color: transparent;
		padding-right: 15px;
		padding-left: 15px;
	}
}
