$padding-indent-horizontal: 15px;

.section-blog_page{
	padding-top: 100px;
}
.blog{
	&-recommend{
		padding-top: 90px;
		padding-bottom: 100px;
	}
}
.blog-item {
	box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
	margin: 12px 10px;
	&.-grey{
		box-shadow: none;
		background: #f8f8f8;
		margin-bottom: 30px;
		padding: 20px 0 10px 0;
		&:last-child{
			margin-bottom: 0;
		}
	}
	._header {
		position: relative;

		.img-wrap {
			position: relative;
			display: block;
			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 35%;
				background: url(../images/diagonal-bg.svg) no-repeat 0 0 / 100% 100%;
			}

			& + .date {
				// position: relative;
				padding-top: 0;
				position: absolute;
				bottom: 0;
				left: 0;
			}
		}

		.date {
			font-size: 16px;
			display: block;
			padding: 15px $padding-indent-horizontal 0;
		}
	}

	._body {
		padding: $padding-indent-horizontal;
	}

	.title {
		position: relative;
		font-size: 23px;
		font-weight: 900;
		text-transform: none;
		padding-bottom: 20px;
		margin-bottom: 20px;
		color: #000;
		display: block;
		&:after {
			content: '';
			display: block;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 4px;
			background-color: map-get($pallete, home01);
		}
	}

	.relative {
		.title {
			position: absolute;
			left: 25px;
			right: 25px;
			top: 25px;
			margin: 0;
			color: #fff;

			&.-bottom {
				top: auto;
				bottom: 25px;
			}
		}

		&:before {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background-color: rgba(#000, .5);
		}
	}

	p {
		&:last-child {
			margin-bottom: 0;
		}
	}

	._footer {
		text-align: right;
		padding: $padding-indent-horizontal;

		a {
			display: inline-block;
			font-size: 18px;
			font-weight: bold;
			color: #000;
			vertical-align: middle;
			text-decoration: none;
			transition: all .3s;

			&:hover {
				color: $primary;
				text-decoration: none;

				span {
					text-decoration: none;
				}
				& i {
					// color: #000;
					transition: none;
				}
			}
			& i {
				// transition: all .3s;
			}
			.fa {
				margin-left: 10px;
				// transition: all .3s;
				vertical-align: middle;
			}
		}
	}

	&.-inverse {
		background-color: #000;
		color: #fff;
		& .title{
			color: #fff;
		}

		._footer {
			a {
				color: #fff;
			}
		}
	}

	&.-default {
		._header {
			.img-wrap {
				&:after {
					display: none;
				}

				+ .date {
					position: static;
				}
			}
		}

		._body {
			.date {
				display: block;
				padding: 0 0 15px;
			}

			.descr {
				overflow: hidden;
				height: 72px;
			}
		}

		._footer {
			a {
				& i {
					// transition: all .3s;
					
				}
				&:hover {
					span {
						text-decoration: none;
					}

					.fa {
						// color: map-get($pallete, blue);
					}
				}

				.fa {
					// transition: color .2s ease-in-out;
					// transition: all .3s;
				}
			}
		}
	}

	&.-wide {
		display: flex;
		align-items: flex-start;
		box-shadow: none;
		margin-bottom: 50px;
		& .blog-item{
		}
		& ._body{
			padding-top: 0;
			padding-bottom: 0;
		}
		& .img-wrap{
			max-width: 50%;
			&  img {
				box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
			}
		}
	}

	&.-no-shadow {
		box-shadow: none;
	}
}

.blog{
	&-title{
		font-size: 37px;
		margin-bottom: 15px;
	}
	&-header{
		&_top{
			display: flex;
			justify-content: space-between;
			margin-bottom: 35px;
			font-size: 14px;
			& i {
				padding-right: 5px;
			}
		}
	}
	&-img{
		max-width: 100%;
		width: 100%;
		margin: 0 auto;
		display: block;
		margin-bottom: 30px;
	}
	&-content{
		margin-bottom: 50px;
		& p {
			font-size: 18px;
			line-height: 32px;
		}
	}
	&-comment{
		&_title{
			font-size: 22px;
			text-transform: capitalize;
			margin-bottom: 20px;
		}
		&_item{
			display: flex;
			margin-bottom: 15px;
			& p {
				margin-bottom: 0;
			}
		}
		&_img{
			flex-basis: 53px;
			max-width: 53px;
			margin-right: 15px;
			min-width: 53px;
		}
		&_descr{
			width: 100%;
		}
		& textarea.form-control{
			height: 53px;
			max-height: 100px;
			background-color: #f1f1f1;
			border: none;
			box-shadow: none;
			font-size: 18px;
			color: #000;
    		padding: 15px 22px;
			&:focus{
				transition: all .3s;
			    background: #fff;
			    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.19);
			}
		}
		& form{
			margin-bottom: 15px;
		}
		&_name{
			font-size: 19px;
			font-weight: 700;
			margin-bottom: 10px;
		}
	}
}
.section-blog {
	padding: 60px 0;
	
	.btn-wrapper {
		padding-top: 30px;
	}
	&.-home02 {
		& .section-heading{
			padding-right: 170px;
		}
	}
	&.-home04 {
		.blog-item ._footer a {
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;
		}
	}
}
@media (max-width: 1300px){
	.section-blog{
		&.-home02{
			& .section-heading{
				display: block !important;
				padding-right: 0;
				& .nav{
					justify-content: center;
				} 
				& .title-link{
					position: static;
					text-align: center;
					display: block;
					transform: none;
				}
			}
		}
	}
}
@media (max-width: 992px){
	.blog-title{
		font-size: 32px;
	}

}
@media (max-width: 767px){
	.section-blog_page{
		padding-top: 50px;
	}
	.blog-title{
		font-size: 28px;
	}
	.blog{
		&-recommend{
			padding-top: 50px;
			padding-bottom: 50px;
		}
	}

}
@media (max-width: 600px){
	.blog-item{
		&.-wide{
			display: block;
			& .img-wrap{
				max-width: 100%;
				margin-bottom: 20px;
			}
			& ._body{
				padding: 0;
			}
		}
	}
}