.card {
	box-shadow: 0 4px 0 #000 inset;
	border: none;
	border-radius: 0;
	background-color: #fff;

	.card-header {
		padding: 45px 15px 45px 60px;

		h4 {
			margin: 0;
			line-height: 30px;
		}
	}

	.card-body {
		&.-indent {
			padding: 30px 45px 0 45px;
		}
	}

	.card-footer {
		padding: 30px 15px;

		> a {
			display: inline-block;
			font-size: 18px;
			font-weight: 700;

			&:hover {
				text-decoration: none;
			}

			.fa {
				color: map-get($pallete, home01);
				margin-left: 7px;
			}

			&:not(.btn-black) {
				transition: color .2s, border-color .2s, background-color .2s;
				color: #000;

				&:hover {
					color: map-get($pallete, home01);
				}
			}
		}
	}

	.card-price {
		span {
			display: inline-block;
			vertical-align: middle;
		}

		.small {
			font-size: 19px;
			margin-right: 5px;
			color: #000;
		}

		.price {
			font-size: 30px;
			line-height: 1;
			font-weight: 900;
			color: #000;
		}

		&.-indent {
			padding-top: 30px;
		}
	}

	.level {
		text-align: center;

		.fa {
			font-size: 20px;
			margin: 0 7px;
			color: map-get($pallete, home01);
		}
	}

	&.w370 {
		max-width: 370px;
		width: 100%;
	}

	&.-ghost {
		background-color: transparent;
		color: #fff;
		box-shadow: none;

		.list-schedule {
			> li {
				&:hover {
					background-color: transparent;
				}

				.time {
					&:after {
						background-color: #fff;
					}
				}
			}
		}
	}

	&.-with-img {
		box-shadow: 0 8px 10px 1px rgba(#000, .14), 0 3px 14px 2px rgba(#000, .12), 0 5px 5px -3px rgba(#000, .3);
		margin: 30px 0;

		.card-header {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			height: 150px;
			background-position: center center;
			background: no-repeat;
			background-size: cover;
			box-shadow: 0 -4px 0 #000 inset;

			&.-lg {
				height: 200px;
			}
		}

		.card-title {
			position: relative;
			font-size: 32px;
			color: #fff;
			font-weight: 900;
			padding-bottom: 20px;

			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 40px;
				height: 4px;
				margin-left: -20px;
				background-color: #000;
			}
		}
	}

	&.-yellow {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, yellow) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, yellow);
			}
		}
	}

	&.-blue {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, blue) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, blue);
			}
		}
	}

	&.-orange {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, orange) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, orange);
			}
		}
	}

	&.-purple {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, purple) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, purple);
			}
		}
	}

	&.-green {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, green) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, green);
			}
		}
	}

	&.-deeppink {
		.card-header {
			box-shadow: 0 -4px 0 map-get($pallete, deeppink) inset;
		}

		.card-title {
			&:after {
				background-color: map-get($pallete, deeppink);
			}
		}
	}

	&.-product {
		box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
		transition: all .35s;
		&:hover{
			box-shadow: 0 0 13px 5px rgba(0, 0, 0, 0.23);
			& ._footer{
				& a {
					text-decoration: none;
					background-color: map-get($pallete, home01);
				}
			}
		}
		.title {
			font-size: 22px;
			color: #000;
			font-weight: 900;
		}

		.info {
			display: block;
			padding: 0 0 20px;
			font-size: 18px;
			color: #7f7f7f;
		}

		.stars {
			padding-bottom: 30px;
		}

		.price {
			font-size: 24px;
			font-weight: 900;

			i {
				font-style: normal;
				font-weight: 700;
				font-size: 22px;
			}
		}

		._body {
			padding: 15px 0 0;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			// height: 240px;
		}

		._footer {
			display: flex;
			justify-content: flex-end;
			text-align: right;

			.link-wrap {
				position: relative;

				&:before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 70px 70px 0 0;
					border-color: #fff transparent transparent transparent;
					pointer-events: none;
				}
			}

			a {
				display: block;
				color: #000;
				background-color: #c4c4c4;
				padding: 35px 13px 13px 37px;
				font-size: 22px;
				transition: background-color .2s ease-in-out;

			}
		}
	}

	&.-product-wide {
		background-color: transparent;

		.title {
			font-size: 20px;
			margin: 0;
			color: #000;
			font-weight: 900;
			padding-right: 15px;
		}
		& fieldset{
			min-width: 100px;
		}
		label {
			margin: 0;
			&:before{
				margin: 0;
				margin-right: 5px;
				font-size: 16px;
			}
		}

		._body {
			padding: 7px 0;
		}

		.info {
			font-size: 18px;
			color: #7f7f7f;
		}

		.price {
			font-size: 24px;
			font-weight: 900;

			span {
				font-weight: 700;
			}
		}

		.link {
			color: #000;
			font-weight: 900;
			font-size: 16px;
			text-transform: uppercase;

			&:hover {
				text-decoration: none;
			}

			.fa {
				margin-left: 7px;
			}
		}
	}

	&.-with-hover {
		transition: background-color .2s ease-in-out;

		&:hover {
			background-color: #f6f6f6;

			.list-custom {
				li {
					span {
						&:before {
							opacity: 1;
						}
					}
				}
			}
		}
		
	}

	&.-short {
		padding-bottom: 30px;
		.card-price.-indent {
			padding: 30px 0;
		}

		.card-header {
			flex-direction: column;
			color: #fff;
		}

		.card-title {
			margin-bottom: 5px;
		}

		.subtitle {
			margin: 0 0 5px;
			text-transform: uppercase;
		}
	}

	&.-product-custom {
		box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
		position: relative;
		overflow: hidden;
		& .title{
			color: #000;
			font-weight: 900;
		}
		.card-overlay_new{
		    position: absolute;
		    top: 0px;
		    right: -10px;
		    font-size: 17px;
		    font-weight: 900;
		    color: #000;
		    text-transform: uppercase;
		    padding: 10px 10px 5px 10px;
		    transform: rotate(45deg);
		    &:before{
			    content: '';
			    display: block;
			    position: absolute;
			    left: -20px;
			    right: -20px;
			    top: -20px;
			    bottom: 0;
			    z-index: -1;
		    }
		}

		.img-wrap {
			height: 370px;
			padding: 30px;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		._body {
			position: relative;
			padding: 40px 0;
			background-color: #f2f2f2;
			transition: padding .3s ease-in-out;

			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background-color: #fff;
				box-shadow: 0 -4px 0 map-get($pallete, green) inset;
				transition: height .3s ease-in-out;
			}

			.info {
				color: #7f7f7f;
			}

			.price {
				padding: 25px 0;
				font-size: 24px;
				font-weight: 900;

				span {
					font-size: 22px;
					font-weight: 700;
				}
			}

			.fa {
				margin: 0 5px;
				font-size: 20px;
				transition: color .2s ease-in-out;

				&:hover,
				&:focus,
				&:focus:active {
					text-decoration: none;
				}
			}
		}

		.rel {
			position: relative;
			z-index: 1;

			> span {
				display: block;
			}
		}

		&:hover {
			._body {
				padding: 30px 0 50px;

				&:after {
					height: 4px;
				}
			}
		}
	}

	&.-fullwidth {

		display: flex;
		justify-content: space-between;
		align-items: center;
		box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
		padding: 40px;
		transition: all .2s ease-in-out;
		min-height: 220px;
		.img-wrap {
			width: 180px;
		}
		&.-hover_bg_none{
			&:hover{
				background: #fff;
				color: #000;
				& .category{
					color: #000;
				}
				& .title{
					color: #000;
				}
			}
		}
		p {
			margin: 0;
		}

		.title {
			font-family: 'Gilroy', sans-serif;
			font-size: 22px;
			font-weight: 900;
			color: #000;
		}

		.category {
			font-family: $font-family-base;
			color: #7f7f7f;
			transition: color .2s ease-in-out;
		}

		.price {
			font-family: 'Gilroy', sans-serif;
			font-size: 28px;
			font-weight: 900;

			i {
				font-family: 'Gilroy', sans-serif;
				font-size: 26px;
				font-weight: 300;
				font-style: normal;
			}
		}

		&:hover {
			color: #fff;
			box-shadow: 0 0 16px 6px rgba(0, 0, 0, 0.18);
			background-color: #202020;

			.category {
				color: #fff;
			}
			& .title{
				color: #fff;
			}
		}
	}

	&.-accordion {
		box-shadow: none;
		background-color: transparent;
		padding: 0;
		margin-bottom: 30px;
		&:last-child{
			margin-bottom: 0;
		}

		.card-header {
			padding: 0;
			a {
				color: #000;
				font-weight: 700;
				position: relative;
				& i {
					position: absolute;
					right: -30px;
					transition: all .3s;
					top: 50%;
					transform: translateY(-50%);
				}
				&[aria-expanded="true"]{
					& i {
						transform: translateY(-50%) rotate( 90deg);

					}
				}
			}
		}

		.card-block {
			padding-left: 20px;
			margin-top: 15px;
			& li {
				margin-bottom: 10px;
				&:last-child{
					margin-bottom: 0;
				}
			}
		}
	}
}

// fieldset, label { margin: 0; padding: 0; }

/****** Style Star Rating Widget *****/

.rating { 
	border: none;
	float: left;
}

.rating > input { display: none; } 
.rating > label:before { 
	content: "\f005";
	margin: 5px;
	font-size: 1.25em;
	font-family: FontAwesome;
	display: inline-block;
}

.rating > .half:before { 
	content: "\f089";
	position: absolute;
}

.rating > label {
	color: #ddd;
	float: right; 
}

/***** CSS Magic to Highlight Stars on Hover *****/

.rating > input:checked ~ label, /* show gold star when clicked */
.rating:not(:checked) > label:hover, /* hover current star */
.rating:not(:checked) > label:hover ~ label {
	color: map-get($pallete, home01); /* hover previous stars in list */
} 

.rating > input:checked + label:hover, /* hover current star when changing rating */
.rating > input:checked ~ label:hover,
.rating > label:hover ~ input:checked ~ label, /* lighten current selection */
.rating > input:checked ~ label:hover ~ label {
	color: map-get($pallete, home01);
}

@media (max-width: 1500px){
	.card{
		& .card-header{
			padding: 25px 15px 25px 60px;
		}
		& .card-footer{
			padding: 15px;
		}
		&.-fullwidth{
			padding: 20px;
			& .price{
				font-size: 22px;
				&-wrapper{
					min-width: 75px;
				}
				& i {
					font-size: 22px;
				}
			}
		}	
	}
}
@media (max-width: 1300px){
	.card.-product-wide{
		& .title{
			font-size: 17px;
		}
		& .info{
			font-size: 15px;
		}
	}
}
@media (max-width: 992px){
	.card.-fullwidth{
		flex-wrap: wrap;
		& .img-wrap{
			max-width: 100%;
			width: 100%;
			margin-bottom: 20px;
		}
	}
}

@media (max-width: 767px){
	.card.-fullwidth{
		& .btn-wrapper{
			display: block;
			text-align: center;
			flex-basis: 100%;
		}
	}
	.card.w370{
		max-width: 100%;
		width: 100%;
		margin-bottom: 30px;
	}
}
@media (max-width: 500px){
	.card.-fullwidth{
		& .info , & .color-type , & .price-wrapper , & .btn-wrapper{
			flex-basis: 100%;
			text-align: center;
			margin: 10px 0;
		}
		& .info{
			& .rating{
			    float: none;
			    margin: 0 auto;
			    text-align: center;
			    display: inline-block;
			}
		}
	}
	.card{
		& .card-header{
			padding: 25px 10px 25px 25px;
		}
		& .card-footer{
			padding: 15px 10px;
		}
	}
}