body{
	// min-width: 320px;
}
.main{
	&.-inner{
		padding-top: 50px;
		& h2 {
			margin-bottom: 20px;
		}
	}
}
@media (max-width: 1500px){
	h1{
		font-size: 65px;
	}
	h5.lined {
		font-size: 16px;
		padding-left: 15px;
	}
}
@media (max-width: 992px){
	h1{
		font-size: 50px;
	}
}
@media (max-width: 767px){
	h1{
		font-size: 40px;
	}
	h2 {
		font-size: 1.7rem;
	}
}
@media (max-width: 500px){
	h1 {
		font-size: 35px;
	}
	h2 {
		font-size: 1.5rem;
	}
	h4 {
		font-size: 1.2rem;
	}
}