.section-top {
	&.-home01.-home01 {
		padding-top: 0;
		.row {
			height: calc(100vh - 80px);
		}

		.subheading {
			margin-bottom: 60px;
		}

		.heading-wrapper {
			padding-bottom: 70px;
		}
	}

	&.-home-02.-home-02 {
		position: relative;
		background-repeat: no-repeat;
		background-position: center top;
		background-size: cover;
		padding-top: 0;

		.full-height {
			position: relative;
			height: calc(100vh - 80px);
		}

		.short-info {
			position: absolute;
			top: 50%;
			right: 8.3333%;
			color: #fff;
			transform: translateY(-50%);
			max-width: 200px;

			.img-wrap {
				max-width: 200px;
				width: 100%;
				& img {
					max-width: 200px;
					width: 100%;
				}
			}

			a {
				color: #fff;
				display: block;
			}
		}
	}

	&.-home-03 {
		background: url(../images/section-top-bg-03.jpg) no-repeat center top / cover;
	}

	&.-home04 {
		padding-top: 80px;

		._content {
			margin: 30px 30px 180px;
			background-color: map-get($pallete, home04);
			box-shadow: -12px 1px 15px rgba(0, 0, 0, 0.24), -17px 2px 50px rgba(0, 0, 0, 0.19);
		}

		.heading-wrapper {
			.subheading {
				color: #000;
			}
		}

		.tab-wrapper {
			transform: translate(0, 50%);
			box-shadow: 0 2px 2px 0 rgba(#000, .14), 0 1px 5px 0 rgba(#000, .12), 0 3px 1px -2px rgba(#000, .2);
		}

		.end {
			align-self: flex-end;
		}
	}
}

@media (max-width: 1500px){
	.section-top{
		&.-home01 {
			.subheading {
				margin-bottom: 30px;
			}
		}
		&.-home-02 .short-info{
			& h3 {
				margin-bottom: 10px;
			}
			& .item{
				padding: 6px 0;
			}
			& .item:nth-child(3) {
				display: none;
			}
			
		}
		&.-home04 {
			& .tab-wrapper{
				transform: translate(0, 20%);
			}
			& ._content{
				margin: 30px 30px 80px;
			}
		}
	}
}
@media (max-width: 1200px){
	.section-top{
		&.-home04{
			& .row {
				& .col-sm-12:first-child{
					display: none;
				}
			}
			& ._content{
				padding-top: 20px;
				margin: 30px 30px 100px;
			}
			& .tab-wrapper{
				transform: translate(0, 20%);
			}
		}
	}
}
@media (max-width: 992px){

	.section-top{
		&.-home-02 .full-height{
			height: inherit;
			display: block;
			padding: 50px 0;
		}
		&.-home-02 .short-info.short-info{
			position: static ;
			transform: none ;
			width: 100%;
			max-width: 100%;
			padding: 0 15px;
			text-align: center;
			margin-top: 30px;
			& .title{
				font-size: 20px;
			}
			& h3 {
				&:before{
					display: none;
				}
			}
			& .item{
				padding: 10px;
				max-width: 200px;
				display: inline-block;
			}
		}
		&.-home-03{
			.card.-ghost{
				max-width: 350px;
				margin: 0 auto;
			}
		}
		&.-home04{
			& ._content{
				margin: 15px 15px 50px;
				padding-bottom: 20px;
			}
			& .tab-wrapper{
				transform: none;
				margin-top: 20px;
			}
		}
	}

}

@media (max-width: 767px){
	.section-top.-home01.-home01 .row , .section-top.-home-02.-home-02 .full-height{
		height: inherit;
	}
	.section-top {
		&.-home01 {
			& .heading-wrapper{
				padding: 30px 0;
			}
			.row {
				height: inherit;
			}
		}
	}
}
