.header {
	min-height: 80px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	box-shadow: 0 12px 15px rgba(0, 0, 0, 0.24);
	background: #fff;
	& .navbar{
		background-color: #fff;
	}
	&-search{
		position: absolute;
		min-width: 250px;
		right: 0;
		bottom: 0;
		z-index: -1;
		transition: all .3s;	
		&.-show{
			bottom: -52px;
			transition: all .3s;
		}
		& input[type="search"]{
			padding-right: 85px;
			box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.19);
			background-color: #f1f1f1;
			border: 0;
			padding: 10px 15px;
			border: 4px solid #fff;
			transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
			// &:focus{
		 //    	transition: all .3s;
			//     background: #fff;
			//     box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.19);
			// }
		}
		::-webkit-input-placeholder { /* Chrome/Opera/Safari */
		  color: rgba(0,0,0, .3);
		}
		::-moz-placeholder { /* Firefox 19+ */
		  color: rgba(0,0,0, .3);
		}
		:-ms-input-placeholder { /* IE 10+ */
		  color: rgba(0,0,0, .3);
		}
		:-moz-placeholder { /* Firefox 18- */
		  color: rgba(0,0,0, .3);
		}
	}
	& .navbar-search{
		position: relative;
	}
	& .navbar-brand{
		min-width: 70px;
	}
    & .btn-search{
        position: absolute;
	    right: 3px;
	    top: 3px;
	    bottom: 3px;
	    cursor: pointer;
	    border: 0;
	    font-weight: 700;
	    min-width: 80px;
    }
	&.fixed-left {
		position: fixed;
		z-index: 10;
		top: 0;
		left: 0;
		width: 125px;
		height: 100%;

		.navbar {
			display: block;
			text-align: center;
			padding: 0;
		}

		.navbar-nav {
			width: 100%;
			flex-direction: column;
			justify-content: space-around;
			height: calc(100vh - 100px);

			.nav-link {
				flex-basis: 100%;
				padding: 15px 5px;
				font-size: 15px;
				
				&.active,
				&:hover {
					box-shadow: -4px 0 0 #000 inset;
				}
			}
		}

		.navbar-brand {
			display: block;
			margin: 0;
			padding: 15px 0;
		}

		.search-link {
			position: absolute;
			top: 15px;
			right: 30px;
		}
	}

	&.dark {
		background-color: #202020;
		box-shadow: 0 12px 15px rgba(0, 0, 0, 0.24), 0 17px 50px rgba(0, 0, 0, 0.19);
		& .navbar{
			background-color: #202020;
		}
		& .navbar-toggler{
			color: #fff;
			border-color: #fff;
		}


		.search-link .fa,
		.navbar-toggleable-md .navbar-nav .nav-link {
			color: #fff;
		}
	}

	&.beige {
		background-color: #f5f5f5;
	}
}

.heading-wrapper {
	.subheading {
		font-size: 22px;
		color: #fff;
	}
}
.drawer-btn{
	display: none;
	& .drawer-hamburger.drawer-hamburger{
		background: #fff;
	    padding: 10px;
	    color: #000;
	    left: 15px ;
	    top: 15px;
	    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	    &:hover{
	    	background: #fff;
	    }
	}
	& i {
		font-size: 18px;
		line-height: 24px;
	}
}
.navbar-toggleable-md {
	padding: 0 45px;

	.navbar-nav {
		.nav-link {
			font-weight: 900;
			font-size: 17px;
			color: #000;
			text-transform: uppercase;
			padding: 27.5px 20px;
			transition: box-shadow .2s;

			&.active,
			&:hover {
				box-shadow: 0 -4px 0 #000 inset;
			}
		}
	}
}

.navbar-toggler {
	border-color: #666;
	color: #666;
}

.theme-02 {
	.navbar-nav {
		.nav-link {
			&.active,
			&:hover {
				box-shadow: 0 -4px 0 map-get($pallete, home01) inset;
			}
		}
	}
}

.search-link {
	.fa {
		color: #000;
		font-size: 20px;
		padding-top: 30px;
		padding-bottom: 30px;
		transition: color .2s;

		&:hover {
			text-decoration: none;
			color: map-get($pallete, home01);
		}
	}
}
.header{
	&.-grey{
			background-color: #f5f5f5;
			& .navbar{
			background-color: #f5f5f5;
		}
	}
}
@media (max-width: 1500px){
	.navbar-toggleable-md {
		padding: 0 15px;
		.navbar-nav {
			.nav-link {
				font-size: 15px;
				padding: 29px 15px;
			}
		}
	}
	.header.fixed-left .navbar-nav .nav-link{
		padding: 10px 5px;
	}
}
@media (max-width: 1300px){
	.navbar-toggleable-md {
		.navbar-nav {
			.nav-link {
				font-size: 14px;
				padding: 29px 7px;
			}
		}
	}
}
@media (max-width: 992px){
	.header{
		&.dark{
			.navbar-collapse{
				margin-top: 70px;
			}
		}
	}
	.navbar-search{
		margin-bottom: 10px;
		& li {
			display: none;
		}
	}
	.header-search{
		display: block;
		position: static;
		z-index: 2;
	}
	.drawer-btn{
		display: block;
	}
	.header.fixed-left .navbar-nav .nav-link{
		padding: 20px 5px;
	}
	.header{
		max-height: 100%;
		overflow-y: auto;
		& .navbar-brand{
			margin-right: 0;
		}
		& .navbar-toggler{
		    margin: 17px 0;
		    cursor: pointer;
    		padding: 10px 12px;

		}
		&.fixed-left{
			left: -125px;
			z-index: 999;
			& .navbar-nav{
				height: inherit;
			}
		}
	}
	.navbar-toggleable-md {
		.navbar-nav {
			.nav-link {
				text-align: center;
				box-shadow: none;
				padding: 15px;
				&.active{
					background:  #f7035b;
					color: #fff;
					box-shadow: none;
				}
				&:hover{
					box-shadow: none;
					color: #fff;
					background:  #f7035b;
				}
			}
		}
	}
}