.filter-range-wrapper {
	padding: 30px 0;
}

.noUi-target {
	height: 5px;
	border: none;
	background-color: #f1f1f1;
}

.noUi-connect {
	background-color: #000;
}

.noUi-handle:before, .noUi-handle:after {
	display: none;
}

.noUi-handle {
	border-radius: 50%;
	background-color: #000;
	box-shadow: none;

	&:focus {
		outline: none;
	}
}

.noUi-horizontal .noUi-handle {
	width: 14px;
	height: 14px;
	border: none;
	left: -7px;
	top: -4px;
}