.btn {
	font-weight: 900;
	text-transform: uppercase;
	color: #000;
	padding: 10px 32px;
	cursor: pointer;
	transition:  all .3s;

	&:focus,
	&:focus ,
	&:active {
		outline: none;
		box-shadow: none;
	}
	&-beige {
		background-color: #f6f6f6;
		border-color: #f6f6f6;
		&:hover{
			color: #000;
			background: darken(#f6f6f6 , 10%)
		}
	}
	&-black{
		background-color: #000;
		border-color: #000;
		color: #fff;

		&:hover,
		&:focus,
		&:active {
			color: #000;
			background-color: #fff;
		}
	}
	&-grey{
		background-color: #e2e2e2;
		border-color: #e2e2e2;
		&:hover,
		&:focus,
		&:active{
			color: #000;
			background: transparent;
		}
	}
	&-primary{
		color: #fff;
		border-color: #1f82b3;
		background-color: #1f82b3;

		&:hover,
		&:focus,
		&:active {
			color: #1f82b3;
			background-color: transparent;
			border-color: #1f82b3;
		}
	}
	&-ico{
		& i {
			padding-left: 10px;
		}
		&_active{
			& i {
				color: $primary;
			}
		}
	}
}
.link{
	&-view{
		color: #000;
		font-weight: 900;
		& i {
			padding-left: 10px;
		}
	}
}
.-abs_icon{
	position: relative;
	padding-right: 13px;
	& i {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}
@media (max-width: 768px){
	.btn{
		padding: 10px 20px;
	}
}