
.section-404 {
	height: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
}

.page_404 {
    &-block {
    	position: absolute;
        color: #fff;
        padding-bottom: 30px;
	    top: 50%;
	    -webkit-transform: translateY(-50%);
	    transform: translateY(-50%);
	    left: 0;
	    right: 0;
	    text-align: center;
	    padding: 30px 0;
	    font-family: 'Lato';
	    font-weight: 900;
    }
    &-title {
        font-size: 100px;
        line-height: 100px;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    &-descr {
        margin-bottom: 10px;
        text-transform: uppercase;
        font-size: 27px;
        font-weight: 900;
    }
}
.-page_404{
    height: calc(100% - 80px);
}
@media (max-width: 992px){
    .page_404 {
        &-title {
            font-size: 80px;
            line-height: 100px;
        }
        &-descr{
            font-size: 25px;
        }
    }
}
@media screen and (max-width: 400px) {
    .page_404 {
        &-title {
            font-size: 50px;
            line-height: 70px;
        }
        &-descr{
            font-size: 22px;
        }
    }
}