.section-coaches-single {
	padding: 50px 0;

	.slider-coaches .item {
		color: #000;
		padding: 10px 0;
		position: relative;
		&:before{
			content: '';
			display: block;
			transition: all .35s;
			position: absolute;
			right: 0;
			bottom: 10px;
			top: 30px;
			background: #f1f1f1;
			left: 100px;
			box-shadow: 0 8px 13px rgba(0,0,0, .2);
			z-index: -1;
			opacity: 0;
		}
		&.-half{
			width: 50%;
		}
		&:hover{
			&:before{
				transition: all .35s;
				opacity: 1;
			}
		}
	}

	.slider-coaches .item .slider-item-content {
		padding: 0 15px;
	}

	.slider-coaches .list-social a {
		color: #000;
	}
	
	.slider-coaches {
		padding: 0;
	}
}