.tab-wrapper {
	&.-with-height {
		min-height: 300px;
	}

	&.-default {
		background-color: #f7f7f7;
	}
}

.tabs-default {
	.nav-item {
		flex-basis: 100%;
		text-align: center;
	}

	.nav-link {
		background-color: #202020;
		color: #fff;
		font-size: 20px;
		font-family: 'Gilroy', sans-serif;
		font-weight: 900;

		&.active {
			background-color: #f7f7f7;
			color: #000;
		}
	}
}

.tab-pane {
	padding: 30px;
}

.nav-tabs {
	&.-primary {
		.nav-link {
			position: relative;
			text-transform: uppercase;
			font-size: 20px;
			color: #000;
			font-weight: 900;

			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: 0;
				left: 50%;
				margin-left: -41px;
				width: 82px;
				height: 4px;
				background-color: #ddd;
				opacity: 0;
				transition: opacity .2s ease-in-out;
				pointer-events: none;
			}

			&.active,
			&:hover,
			&:focus,
			&:focus:active {
				border-color: transparent;
				background-color: transparent;

				&:after {
					opacity: 1;
				}
			}
		}
	}
}
@media (max-width: 600px){
	.tab-wrapper.-with-height.-default{
		& .nav{
			flex-wrap: wrap;
		}
		& .list-schedule.-custom > li{
			flex-basis: 100%;
			max-width: 100%;
		}
		& .tab-pane{
			padding: 20px;
		}
	}
}