.section-contact {
	position: relative;
	color: #fff;

	&.-home02 {
		background: url(../images/section-contact-bg-01.jpg) no-repeat center 0 / cover;
		padding-bottom: 40px;
	}

	.contact-content {
		border-top: 4px solid #666;
		display: flex;
		height: 100vh;
		padding: 0;
	}

	.half {
		display: flex;
		height: 100%;
		flex-basis: 50%;
		max-width: 50%;

		&.-content {
			flex-direction: column;
			justify-content: space-around;
			// align-items: center;
			background: url(../images/section-testimonials-bg-01.jpg) no-repeat center 0 / cover;
			padding-left: 8.333%;
			padding-right: 100px;
		}
	}

	.section-heading {
		padding: 0;

		h2 {
			margin-bottom: 25px;
		}

		.subheading {
			font-size: 20px;
			margin: 0;
			padding-left: 24px;
		}
	}

	.item {
		&.-pl {
			padding-left: 24px;
		}

		&.-pb {
			padding-bottom: 30px;
		}

		&.-info {
			font-size: 20px;
			p {
				margin: 0 0 7px;
			}
		}
	}

	h3 {
		font-size: 23px;
		text-transform: uppercase;
		margin-bottom: 25px;
	}

	a {
		color: #fff;
	}

	.social {
		a {
			margin: 0 10px;
			font-size: 20px;

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}

			&:hover {
				color: map-get($pallete, home01);
			}
		}
	}

	.list-contact {
		padding-bottom: 40px;
	}

	&.-home03 {
		color: #000;
		padding-bottom: 50px;

		a {
			color: inherit;
		}

		.list-contact {
			padding-left: 0;
		}

		.map-wrapper.-fix-size {
			height: 260px;	
		}

		.-pt-indent {
			padding-top: 50px;
		}

		.form-contact {
			padding: 0;
		}
	}

	&.-home04 {
		position: relative;
		height: calc(100vh - 90px);
		margin: 45px;
		background-color: #202020;
		box-shadow: 0 4px 5px 0 rgba(#000, .14), 0 1px 10px 0 rgba(#000, .12), 0 2px 4px -1px rgba(#000, .3);

		.fullheight {
			height: 100%;
		}

		.halfheight {
			height: 50%;
		}

		.-bordered {
			border-bottom: 4px solid map-get($pallete, home04);
		}
		
		.form-contact {
			width: 50%;
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 1;
			transform: translate(-50%, -50%);
		}

		.item-contact {
			padding: 45px 0;
		}
	}
}

.block-rotate {
	width: 265px;
	height: 265px;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 1;
	background-color: #fff;
	color: #000;
	transform: translate(-50%, -50%) rotate(45deg);

	.text-rotate {
		transform: rotate(-45deg);
		text-align: center;
	}

	h3 {
		margin: 0 0 10px;
	}

	p {
		font-size: 20px;
		margin: 0 0 7px;
	}
}
.section-contact{
	&.-home03{
		& .section-heading {
			margin-bottom: 0;
			padding: 60px 0;
		}
	}
}
@media (max-width: 1500px){
	.section-contact.-home04{
		margin: 15px;
	}
} 
@media (max-width: 992px){
	.section-contact.-home04{
		height: inherit;
	}
	.section-contact.-home04 .form-contact{
		position: static;
		transform: none;
		width: 100%;
		padding: 40px 20px;
	}
	& .section-contact.-home04 .halfheight{
		height: inherit;
		padding: 50px 15px;
	}
	.section-contact.-home04{
		margin: 0;
		margin-top: 30px;
	}
	.section-contact{
		.contact-content{
			display: block;
			height: inherit;
			& .half{
				max-width: 100%;
				&.-content{
					padding: 50px 15px;
				}
				& #map{
					min-height: 400px;
				}
			}
		}
		
	}
	.block-rotate{
		position: static;
		transform: none;
		width: 100%;
		height: inherit;
		padding: 20px 0;
		& .text-rotate{
			transform: none;
		}
	}

}
@media (max-width: 767px){
	.section-contact.-home03 .map-wrapper.-fix-size{
		margin: 30px 0;
	}
	.section-contact{
		&.-home02{
			.map-wrapper{
				margin-top: 20px;
			}
		}
	}
}
@media (max-width: 500px){
	.section-contact .section-heading .subheading , .section-contact .item.-pl{
		padding-left: 0;
	}
	.section-contact.-home04 .form-contact{
		padding: 20px 10px;
	}
}