.section-coaches-single {
	.classes-single-item ._header .title {
		color: map-get($pallete, home03);
	}

	.classes-single-item { 
			background-color: #f5f5f5;
		    box-shadow: 0 3px 13px 2px rgba(0, 0, 0, 0.27);
		    padding: 0;
		    margin: 20px;
		    align-items: flex-start;
		&.stretch {
			align-items: stretch;
		}

		.item-description {
			box-shadow: none;
		}

		._footer {
			padding-top: 15px;
		}
	}

	.social a {
		color: #000;
		margin-right: 15px;
	}

	&.-dark {
		.classes-single-item {
			.item-description {
				background-color: #202020;
				color: #fff;
			}
		}

		.social {
			a {
				color: #fff;
			}
		}
	}
}
@media (max-width: 1400px){
	.section-coaches-single{
		& .classes-single-item {
			align-items: center;
		}
	}
}