.panel {
	&.-step {
		padding-bottom: 15px;
		height: 100%;
		box-shadow: 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12), 0 3px 5px -1px rgba(0,0,0,0.3);

		.panel-heading {
			padding: 15px 20px;
			background-color: #000;
			color: #fff;
			text-align: center;

			span {
				display: inline-block;
				vertical-align: middle;
			}

			.num {
				width: 36px;
				height: 36px;
				margin-right: 7px;
				background-color: #dedede;
				color: #000;
				border-radius: 50%;
				text-align: center;
				line-height: 36px;
			}

			h4 {
				margin: 0;
			}
		}

		.panel-body {
			padding: 45px;
		}

		.link {
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;
			color: #000;
			text-transform: uppercase;

			&:hover {
				text-decoration: none;
			}

			.fa {
				margin-left: 7px;
			}
		}

		.total {
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;

			.text {
				font-size: 21px;
			}

			.price {
				font-size: 27px;

				i {
					font-style: normal;
					font-size: 21px;
					font-family: 'Gilroy', sans-serif;
					font-weight: 300;
				}
			}
		}
	}
}
@media (max-width: 1500px){
	.theme04 .panel.-step .panel-heading .num{
		display: block;
		margin: 0 auto;
		margin-bottom: 10px;
		text-align: center;
	}
	.panel.-step{
		& .panel-body{
			padding: 20px;
		}
		& h5 {
			font-size: 16px;
		}
	}
}
@media (max-width: 992px){
	.panel.-step .panel-body{
		padding: 20px 10px;
	}
	.step-item{
		margin-bottom: 20px;
		&:last-child{
			margin-bottom: 0;
		}
	}
}