$primary: map-get($pallete, home01);

.theme01 {
	& .modal-price_col_descr{
		border-right: 3px solid $primary;
	}
	.btn-primary{
		background: $primary;
		border-color: $primary;
		&:hover , 
		&:focus ,
		&:focus:active{
			color: $primary;
			border-color: $primary;
		}
	}
	& .timetable-wrap.-default .timetable-item:hover{
		border-color: $primary;
	}
	& .slider-quotes .item .social a{
		&:hover{
			color: $primary;
		}
	}
	& .search-link .fa{
		&:hover{
			color: $primary;
		}
	}
	& .btn-search{
		background: $primary;
		transition: all .3s;
		color: #fff;
		&:hover{
			background: darken($primary , 10%);
		}
	}
	& .card.-product-wide .link{
		&:hover{
			color: $primary;
		}
	}
	& .list-social{
		& a {
			transition: all .3s;
			&:hover{
				color: $primary;
			}
		}
	}
	.title-link {

			.link {
				&:hover{
					color: $primary;
				}
			}
		}
	& .coach{
		&-content{
			&_bot{
				& .list-social{
					& a {
						&:hover{
							color: $primary;
						}
					}
				}

			}
		}
	}
	& .link{
		&-view{
			&:hover{
				color: $primary;
			}
		}
	}
	& .btn-beige{
		&:hover{
			background: darken(#f6f6f6 , 10%);
			color: #000;
		}
	}
	& .blog-item{
		& ._footer {
			& a {
				&:hover {
					color: $primary;
				}
			}
		}
	}
	& .btn{
		&-link{
			&:hover{
				color: $primary;
			}
		}
		&-ico{
			&_active{
				& i {
					color: $primary;
					
				}
			}
		}
	}
	& .section-coaches-single .social a{
		&:hover{
			color: $primary;
		}
	}

	.navbar-toggleable-md .navbar-nav .nav-link.active, .navbar-toggleable-md .navbar-nav .nav-link:hover {
		box-shadow: 0 -4px 0 $primary inset;
	}

	.section-top {
		h1 {
			color: $primary;
		}
	}

	.lined {
		&:before {
			background-color: $primary;
		}
	}

	.card {
		box-shadow: 0 4px $primary inset;
	}

	.slider-content ._header h4 {
		color: $primary;
	}

	.slider-coaches .h2 {
		color: $primary;
	}

	.timetable-col.active .timetable-title {
		color: $primary;
	}

	.gallery-caption .naming {
		color: $primary;
	}

	.slider-testimonials .title {
		color: $primary;
	}

	.blog-item .title:after {
		background-color: $primary;
	}

	.slider-stories .social a {
		color: $primary;
	}

	.zoom-link:hover {
		outline-color: $primary;
	}

	.rating > input:checked ~ label,
	.rating:not(:checked) > label:hover,
	.rating:not(:checked) > label:hover ~ label {
		color: $primary;
	}

	.card.-product ._footer a:hover {
		background-color: $primary;
	}

	.section-coaches {
		box-shadow: 0 4px 0 map-get($pallete, home01) inset;
	}

	.section-contact {
		.contact-content {
			border-color: $primary;
		}
	}

	.section-coaches-single .classes-single-item ._header .title {
		color: $primary;
	}
	.section-classes-single .classes-single-item ._header .title {
		color: $primary;
	}

	.slider-quotes .item .img-wrap {
		border-color: $primary;
	}
	.slider-quotes .quote:before, .slider-quotes .quote:after {
		color: $primary;
	}

	.nav-tabs.-primary .nav-link:after {
		background-color: $primary;
	}
}