@font-face {
	font-family: 'icomoon';
	src:
		url('../fonts/icomoon/icomoon.ttf?ylcktk') format('truetype'),
		url('../fonts/icomoon/icomoon.woff?ylcktk') format('woff'),
		url('../fonts/icomoon/icomoon.svg?ylcktk#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="im-"], [class*=" im-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

