.timetable-wrap {
	display: flex;
	flex-wrap: wrap;
	margin-left: -10px;
	margin-right: -10px;

	&.-custom {
		.timetable-col {
			padding: 0;
		}

		.timetable-cell {
			height: 115px;
			padding: 0;
			margin: 15px 0;
		}

		.timetable-time {
			display: inline-block;
			margin: 0 0 10px;
			font-family: $font-semibold;
			font-size: 17px;
			transform: translateX(-50%);
		}

		.timetable-item {
			height: auto;
			padding: 10px 0;
			border-top: 1px solid #d7d7d7;
			border-bottom: 1px solid #d7d7d7;
			box-shadow: 3px 0 0 #000 inset;
			transition: background-color .2s ease-in-out, opacity .2s ease-in-out, visibility .2s ease-in-out;

			&:hover {
				span {
					color: #000;
				}

				background-color: #f0f0f0;
			}

			&:before {
				display: none;
			}
		}
	}

	&.-default {
		.item-body {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
		}
		.timetable-cell {
			height: 110px;
		}

		.timetable-item {
			background-color: #f0f0f0;
			border-left: 4px solid #000;
			transition: border-color .2s ease-in-out, opacity .2s ease-in-out, visibility .2s ease-in-out;
			padding: 12px;

			&:hover {
				border-color: #666;
				span {
					color: #000;
				}
			}

			.naming {
				font-size: 18px;
				font-weight: 700;
			}

			.time {
				margin: 0;
				font-weight: 700;
			}

			.item-footer {
				width: 100%;
				text-align: right;
			}

			&:before {
				display: none;
			}
		}
	}

	&.-simple {
		.timetable-item {
			background-color: #f3f3f3;
			transition: background-color .2s ease-in-out, opacity .2s ease-in-out, visibility .2s ease-in-out;
			
			&:before {
				display: none;
			}

			&:hover {
				span {
					color: #000;
				}
			}
			
			.naming {
				font-family: 'Gilroy', sans-serif;
				font-weight: 900;
			}
		}
	}
}

.timetable-time_mob.timetable-time_mob{
	display: none;
}
.timetable-col {
	flex: 1;
	flex-shrink: 0;
	padding-left: 10px;
	padding-right: 10px;

	&.active {
		.timetable-title {
			color: map-get($pallete, purple);
		}
	}

	&.-with-add {
		flex-shrink: 1; 
		flex-basis: 1;
	}

	&.-add {
		flex-basis: 90px;
		padding-top: 34px;
		max-width: 90px;
		.timetable-cell {
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
}

.timetable-cell {
	height: 160px;
	padding-top: 15px;
	padding-bottom: 15px;
}

.timetable-title {
	padding-bottom: 15px;
	text-align: center;

	h5 {
		margin: 0;
	}
}

.timetable-item {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	text-align: center;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	opacity: 1;
	visibility: visible;
	transition: opacity .2s ease-in-out, visibility .2s ease-in-out;

	&.hide {
		opacity: 0;
		visibility: hidden;
	}

	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 1;
		visibility: visible;
		background-color: #f0f0f0;
		box-shadow: 4px 0 0 #000 inset;
		transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
	}

	&:hover {
		&:before {
			opacity: 0;
			visibility: hidden;
		}

		span {
			color: #fff;
		}
	}

	span {
		display: block;
		transition: color .2s ease-in-out;
	}

	.naming {
		font-size: 22px;
	}

	.coach {
		font-size: 18px;
	}

	.time {
		font-size: 18px;
		font-weight: 700;
		margin-top: 10px;
	}

	.item-body {
		position: relative;
		z-index: 1;
	}
}

.section-timetable {
	padding: 70px 0;

	.nav {
		a {
			color: #535353;
			font-size: 20px;
			transition: text-shadow .2s;

			&.active {
				color: #000;
				text-shadow: 1px 0 0 #000, 0px 0 0 #000;
			}
			&:hover{
				text-shadow: 1px 0 0 #000, 0px 0 0 #000;
				color: #000;
				
			}
		}
	}
	

	.-top,
	.-bottom {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-direction: column;
		height: 255px;
	}

	.-bottom {
		height: calc(100vh - 255px);
	}

	.tabs-default.tabs-default {
		position: relative;
		a {
			color: #fff;
			padding-left: 50px;
			padding-right: 50px;
			box-shadow: -2px 1px 10px rgba(0, 0, 0, 0.16);

			&.active {
				background-color: #fff;
				color: #000;
				text-shadow: none;
			}
		}

		.nav-item {
			flex-basis: auto;
		}
	}

	.tab-content {
		position: relative;
		background-color: #fff;
		box-shadow: 0 2px 2px 0 rgba(#000, .14), 0 1px 5px 0 rgba(#000, .12), 0 3px 1px -2px rgba(#000, .2);
	}

	&.-home03 {
		padding-bottom: 0;
		& .section-heading {
			margin-bottom: 0;
			padding: 60px 0;
		}
		& .container-fluid{
			& .row:first-child{
				margin-bottom: 30px;

			}
		}
	}
}
.section-timetable{
	&.-home04{
		.tab-pane{
			padding: 30px 10px;
			// box-shadow: -2px 1px 10px rgba(0, 0, 0, 0.16);
		}
	}
	&.-home03{
		.nav a{
			transition: all .3s;
			&.active , &:hover{
				color: #fff;
				text-shadow: 1px 0 0 #fff, 0px 0 0 #fff;
			}
		}
	}
}
.timetable-wrap.-inner{
	& .timetable-item {
		& .naming{
			font-size: 16px;
		}
		& .time{
			font-size: 16px;
		}
		& .coach{
			font-size: 16px;
		}
	}
}
.timetable-wrap.-simple{

}

@media (max-width: 1500px){
	.timetable-wrap.-inner{
		& .timetable-item {
			padding: 10px 5px;
		}
	}
	.section-timetable{
		&.-home03{
			& .section-heading{
				margin-bottom: 0;
			}
		}
	}
	.timetable-col{
		padding: 0 5px;
		& .timetable-title{
			& h5 {
				font-size: 15px;
			}
		}
	}
	.timetable-item {
		& .naming{
			font-size: 17px;
		}
		& .coach{
			font-size: 15px;
		}
	}
	.section-timetable {
		& .nav {
			& a {
				padding: 10px;
			}
		}
	}
}
@media (max-width: 1300px){
	.section-timetable{
		& .section-heading{
			display: block;
			& .nav{
				margin-top: 10px;
				justify-content: center;
			}
		}
		&.-home03{
			.timetable-wrap{
				display: block;
			}
			.timetable-col{
				display: flex;
				flex-wrap: wrap;
				& .timetable-title{
					width: 100%;
				}
				& .timetable-cell{
					flex: 1;
				}
			}
		}
	}
	.timetable-wrap.-inner{
		display: block;
		& .timetable-item {
			padding: 10px;
		}
		.timetable-col{
			display: flex;
			flex-wrap: wrap;
			& .timetable-title{
				width: 100%;
			}
			& .timetable-cell{
				flex: 1;
			}
		}
	}
}
@media (max-width: 992px){
	.timetable-col.-add{
		max-width: inherit;
	}
	.timetable-cell{
		height: inherit;
		padding: 0;
	}
	.timetable-wrap.-custom {
		& .timetable-cell{
			height: inherit;
			margin: 0;
		}
		& .timetable-item{
			transition: none;
			&.hide{
				height: 0;
				padding: 0;
			}
		}
	}
	.timetable-wrap.-default{
		.timetable-cell{
			height: inherit;
		}
		& .timetable-item{
			transition: none;
			// padding: 0;
			&.hide{
				display: none;
			}
		}
	}
	.timetable-item{
		padding: 15px 0;
		transition: none;
		&.hide{
			height: 0;
			padding: 0;

		}
	}
	.timetable-title{
		// margin: 15px 0;
		margin-top: 10px;
	}
	.section-timetable .tabs-default.tabs-default{
		justify-content: center;
	}
	.section-timetable.-home02{
		& .timetable-wrap {
			padding: 0 20px;
		}
	}
	.section-timetable  .nav{
		flex-wrap: wrap;
		& a {
			flex-basis: 25%;
		}
	}
	.timetable-wrap{
		display: block;
	}
	.timetable-col{
		display: flex;
		flex-wrap: wrap;
		& .timetable-title{
			width: 100%;
			padding-bottom: 15px;
		}
		& .timetable-cell{
			flex: 1;
		}
	}
}
@media (max-width: 767px){
	.timetable-wrap.-simple{
		margin-top: 20px;
		& .timetable-col.-add{
			display: none;
		}
		& .timetable-time_mob{
			display: block ;
			position: absolute;
			top: 2px;
			right: 2px;
			line-height: 1;
			font-size: 12px;
		}
	}
	.section-timetable.-home04 .tab-pane{
		padding: 0 10px;
	}
	.section-timetable .tabs-default.tabs-default{
		& li {
			width: 50%;
		}
	}
	.section-timetable{
		&.-home03{
			.timetable-col{
				& .timetable-cell{
					flex-basis: 50%;
				}
			}
		}
	}
	.timetable-wrap.-inner{
		.timetable-col{
			& .timetable-cell{
				flex-basis: 50%;
			}
		}
	}
	.timetable-col{
		& .timetable-cell{
			flex-basis: 50%;
		}
	}
}
@media (max-width: 500px){
	.section-timetable .tabs-default.tabs-default{
		& li {
			width: 100%;
		}
	}
}