.block-zoom-wrapper {
	padding-bottom: 30px;

	.caption {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 7px 0;
	}
}

.block-zoom {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.zoom-link {
	position: relative;
	max-width: 100px;
	width: 100px;
	height: 100px;
	background-color: #fff;
	transition: transform .2s ease-in-out, box-shadow .2s ease-in-out;

	img {
		max-width: 100px;
		max-height: 100px;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		margin: auto;
	}

	&:hover {
		z-index: 1;
		transform: scale(1.8);
		outline: 4px solid  #000;
	}
}

@media (max-width: 500px){
	.block-zoom{
		flex-wrap: wrap;
		margin: 0 -1%;
		& a {
			max-width: 100%;
			width: 48%;
			margin: 1%;
			&:hover{
				transform: scale(1.1);
			}
		}
	}
}