.modal{
	z-index: 99999;
}
button.close{
    padding: 0;
    cursor: pointer;
    background: transparent;
    border: 0;
    -webkit-appearance: none;
}
.-modal_pricing{
	& .modal-content{
		padding: 40px 30px;
		border-radius: 0;
	}
	& .modal-dialog{
		max-width: 1300px;
		width: 100%;
		padding: 0 5px;
	}
	& .close{
		position: absolute;
		top: 20px;
		right: 20px;
		color: #090909;
		transition: all .3s;

	}
}
.modal-price{
	&_title{
		margin-bottom: 20px;
	}
	&_container{
		display: flex;
	}
	&_col{
		&_descr{
			flex-basis: 55%;
			border-right: 3px solid $brand-primary;
			padding-right: 40px;
		}
		&_form{
			flex-basis: 45%;
			padding-left: 35px;

		}
	}
	&_subtitle{
		font-weight: 700;
		margin-bottom: 5px;
	}
	&_price{
		font-size: 40px;
		line-height: 1;
		margin-bottom: 5px;
	}
	&_descr_item{
		margin-bottom: 10px;
	}
}
@media (max-width: 992px){
	.-modal_pricing{
		& .modal-content{
			padding: 20px 15px;
		}
		& .modal-dialog{
			margin: 0;
		}
	}
	.modal-price{
		&_container{
			display: block;
		}
		&_col{
			&_descr.modal-price_col_descr{
				border: none ;
				padding: 0;
			}
			&_form{
				padding: 0;
				margin-top: 20px;
			}
		}
		&_price{ 
			font-size: 40px;
		}
	}
}

@media (max-width: 767px){
	.-modal_reserv{
		& .modal-content{
			padding: 15px;
		}
	}
}
@media(max-width: 500px){
	.modal-price{
		&_col{
			&_form{
				& .btn{
					display: block;
					max-width: 100%;
					width: 100%;
					margin-top: 15px;
				}
			}
		}
	}
}