.section-coaches_inner{
	// padding: 50px 0 20px 0;
	padding-top: 50px;
	& h2 {
		margin-bottom: 50px;
	}
}

.section-coaches.-home04{
	& .slider-content ._header a{
		color: #fff;
	}
}
.coach{
	&-item{
		display: flex;
		// height: 100%;
		margin-bottom: 50px;
	}
	&-img{
		// position: absolute;
		// top: 0;
		// left: 0;
		// right: 0;
		// bottom: 0;
		// width: 100%;
		// height: 100%;
		&_wrapper{
			position: relative;
			flex-basis: 40%;
		}
	}
	&-content{
		flex-basis: 60%;
		padding-left: 30px;
		position: relative;
		padding-bottom: 50px;
		&_bot{
			position: absolute;
			bottom: 0;
			left: 30px;
			right: 0;
			font-size: 16px;
			color: #000;
			& .list-social{
				float: left;
				& a {
					color: #000;
					font-size: 16px;
				}
			}
			& .link-view{
				float: right;
			}
		}
	}
	&-title{
		font-size: 28px;
		font-weight: 900;
		color: #000;
		margin-bottom: 5px;
		&:hover{
			color: #000;
		}
	}
	&-subtitle{
		font-weight: 20px;
		color: #676767;
		margin-bottom: 15px;
		display: block;
	}
	&-descr{
		margin: 0;
	}
}
.-pd_item_inner{
	margin: 0 -30px;
	& .classes-single-item{
		padding: 30px;
	}
}

.classes-single-item {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 30px 15px;
	position: relative;
	&.-custom{
		& .item{
			&-description{
				// flex-basis: 100%;
				// padding-left: 50%;
			}
		}
	}
	&.-single{
	    box-shadow: 0 3px 13px 2px rgba(0, 0, 0, 0.2);
	    background-color: #f5f5f5;
	    display: block;
	    padding: 40px;
	    margin: 0;
		& .img-wrap{
			// position: static;
			max-width: 40%;
			float: left;
			margin-right: 25px;
		}
		& .item-description{
			padding: 0;
			box-shadow: none;
			background: none;
		}
		& .btn.btn-link{
			padding: 0;
		}
	}
	&.-dark{
		margin: 30px 0;
		background: #202020;
		.img-wrap{
			top: 0;
			bottom: 0;
		}
	}
	&.-classes{
		margin: 30px 0;
	    box-shadow: 0 3px 13px 2px rgba(0, 0, 0, 0.27);
	    padding: 0;
	    & .title{
	    	font-weight: 700;
			font-size: 1.5rem;
	    }
		& .img-wrap{
			flex-basis: 50%;
		}
		& .item-description {
			box-shadow: none;
			flex-basis: 50%;
			padding: 40px;
		}
		& ._body{
			margin-bottom: 10px;
		}
	}

	.img-wrap {
	    flex-basis: 40%;
	}

	.item-description {
		flex-basis: 60%;
		margin-left: auto;
		// padding: 50px 15% 50px 10%;
		padding: 40px;
		box-shadow: 0 3px 13px 2px rgba(0, 0, 0, 0.27);
	}

	p {
		&:last-child {
			margin-bottom: 0;
		}
	}

	.subtitle {
		font-size: 20px;
		font-weight: 700;
	}
	& .title{
		font-size: 1.5rem;
		display: block;
		font-weight: 700;
	}

	._header {
		padding-bottom: 20px;
	}
	& .header-soc{
		padding-left: 10px;
	}
	& ._header_box{
		display: flex;
		justify-content: space-between;
		align-content: flex-start;
	}

	&.-r {
		& .img-wrap{
			order: 2;
		}
		.item-description {
			order: 1;
			margin-left: inherit;
		}
	}
}

.section-coaches {
	padding-top: 60px;
	background: url(../images/section-coaches-img-01.jpg) no-repeat center top / cover;

	.nav {
		a {
			color: #fff;
			transition: text-shadow .2s;
			font-size: 20px;

			&.active {

				text-shadow: 1px 0 0 #fff, 0px 0 0 #fff;
			}
		}
	}

	h2 {
		color: #fff;
	}

	&.-home02 {
		padding-bottom: 60px;
	}

	&.-home03 {
		background: transparent;
		padding-bottom: 60px;

		h2 {
			color: #000;
		}
	}
}
@media (max-width: 1500px){
	.slider-coaches{
		& .slick-prev, & .slick-next{
			width: inherit;
			height: inherit;
		}
	}
	.section-coaches .nav a{
		padding: 10px;
	}
	.slider-coaches{
		padding: 0 30px;
	}
	.section-coaches.-home01 .item .img-wrap img{
		max-width: 175px;
	}
}
@media (max-width: 1300px){
	.section-coaches-single .slider-coaches .item:before{
		display: none;
	}
	.classes-single-item.-classes .item-description{
		padding: 40px;
	}
	.classes-single-item .item-description{
		padding: 50px;
	}
}
@media (max-width: 992px){
	.classes-single-item.-single{
		padding: 0;
		margin: 0;
		& .item-description{
			padding: 0 20px 20px 20px;
		}
		& .img-wrap{
			max-width: 100%;
			width: 100%;
			float: none;
			margin-bottom: 15px;
		}
	}
	.coach{
		&-img_wrapper{
			// padding-bottom: 56.25%;
			max-height: 300px;
			overflow: hidden;
			margin-bottom: 15px;
			& .resp-img{
				background-position: center top;
			}
		}
		&-item{
			display: block;
		}
		&-content{
			padding-bottom: 35px;
			padding-left: 0;
			flex-basis: 100%;
			&_bot{
				left: 0;
			}
		}
	}

	.classes-single-item{
		padding: 30px 8px;
		display: block;
		& .img-wrap{
		    position: relative;
		    // padding-bottom: 56.25%;
		    max-width: 100%;
		    width: 100%;
		    margin: 0 auto;
		    left: inherit;
		    right: inherit;
		    top: inherit;
		    bottom: inherit;
		}
		& .item-description{
			padding: 30px;
		}
		&.-r .img-wrap{
			right: inherit;
		}
		& .resp-img{
			background-position: center top;
		}
	}
	.classes-single-item.-classes{
		display: block;
		& .img-wrap{
		}
		& .btn{
			margin-top: 10px;
		}
	}
}
@media (max-width: 767px){
	.coach-img_wrapper{
		max-height: inherit;
		overflow: inherit;
	}
	.classes-single-item.-single{
		& ._header_box , & .header-soc{
			display: block !important;
			padding-left: 0;

		}
		& .header-soc{
			margin-top: 15px;
		}
		& .social{
			display: inline-block;
		}
	}
	.classes-single-item .item-description{
		padding: 30px 20px;
	}
	.section-coaches-single .slider-coaches .item.-half{
		width: 100%;
	}
	.slider-coaches .list-social {
		margin: 10px 0;
	}
}
@media (max-width: 500px){
	.classes-single-item.-classes{
		& .item-description{
			padding: 30px 20px;
		}
	}
	.classes-single-item .subtitle{
		font-size: 18px;
	}
}