



/// Font path for fontawesome

$fa-font-path:			'../fonts/fontawesome';     

/// Font path for slick slider

$slick-font-path:		'../fonts/slick/';

$font-semibold:			'LatoSemibold', sans-serif;

// Base Font family for theme 

$font-family-base:		'Lato', sans-serif;


/// Basic colors for the theme

$pallete: (
	home01: #f7035b,
	home02: #0072ab,
	home03: #85bf2a,
	home04: #f5d02c,
	yellow: #f7e303,
	blue: #4dbae7,
	orange: #f79e03,
	purple: #bb03f7,
	green: #659b2f,
	deeppink: home01
);

$font-size-h1:			90px;
$headings-font-weight:	900;

$font-size-h5:			18px;

$btn-border-radius:		0;

$slick-arrow-color:		#000;
$slick-font-family:		'FontAwesome';
$slick-prev-character:	"\f104";
$slick-next-character:	"\f105";
$slick-opacity-default:	1;