/*------------------------------------------------------------------
	Global styles
-------------------------------------------------------------------*/

img {
	max-width: 100%;
	height: auto;
}

a{
	transition: all .3s;
	&:hover , &:active , &:focus{
		transition: all .3s;
		text-decoration: none !important;
	}
}

* , 
*:focus , 
*:active , 
*:visited , 
*:hover{
	outline: none !important;
}
html{
	height: 100%;
}

.custom-object-fit {
  position: relative;
  background-size: cover;
  background-position: center center;
  img {
    opacity: 0;
  }
}