.shop{
	&-result.shop-result{
		font-weight: 400;
		font-family: 'Lato';
		text-transform: inherit;
	}
	&-list_items{
		& .col-md-6{
			padding-bottom: 30px;
		}
	}
}
.shop-panel {
	padding: 0px 15px 40px 15px;
}

.shop-info-title {
	display: flex;
	justify-content: space-between;
	padding: 0 0 45px;

	.subheading {
		color: #7f7f7f;
		margin: 0;
	}

	label {
		margin-bottom: 0;
	}

}
.section-shop.-home04{
	& .link {
		color: #000;
	}
}

.shop-info-content {
	.old-price {
		display: inline-block;
		color: #c4c4c4;
		font-weight: 900;
		font-size: 28px;
		text-decoration: line-through;
	}

	.new-price {
		display: inline-block;
		color: #000;
		font-weight: 900;
		font-size: 36px;
	}

	.description {
		padding: 15px 0;

		p {
			line-height: 1.8;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

.section-cart-single {
	padding: 50px 0;
}
.section-shop {
	padding: 60px 0;
	&.-home02{
		&.-inner{
			background: transparent;
		}
		background: #f0f0f0;
		.section-heading{
			padding-right: 0;
		}
	}
	.section-heading {
		position: relative;

		padding: 0 170px 0 0 ;
		margin-bottom: 30px;
	}
}
.section-shop.-home02{
	.section-heading{
		padding-right: 170px;
	}
}
.section-shop-single {
	padding: 50px 0;

	.nav-tabs {
		padding-top: 40px;
	}

	.tab-content {
		padding-bottom: 40px;
	}

	.card {
		margin-top: 15px;
		margin-bottom: 15px;
	}
}
@media (max-width: 1300px){
	.section-shop.-home02 .section-heading{
		padding-right: 0;
	}
	.section-shop .section-heading{
		display: block !important;
		padding-right: 0;
		& .nav{
			justify-content: center;
		} 
		& .title-link{
			position: static;
			text-align: center;
			display: block;
			transform: none;
		}
	}
}
@media (max-width: 992px){
	.nav-tabs.-primary .nav-link{
		font-size: 18px;
	}
}
@media (max-width: 767px){
	.nav.nav-tabs.-primary{
		flex-wrap: wrap;
	}
	.nav-tabs.-primary .nav-item{
		width: 100%;
	}
	.section-shop-single{
		.tab-pane{
			padding: 20px 0;
		}
	}
}
@media (max-width: 500px){
	.shop-panel{
		& > div{
			display: block !important;
		}
		& .select-wrapper{
			display: inline-block;
		}
	}
	.shop-result.shop-result{
		width: 100%;
		margin-bottom: 15px;
	}
}