$primary: map-get($pallete, home03);
.theme03 {
	& .modal-price_col_descr{
		border-color: $primary;
	}
	& .list-schedule > li a{
		color: #fff;
	}
	& .slider-classes-for .item .slider-content ._header a{
		color: $primary;
	}
	& .search-link .fa{
		&:hover{
			color: $primary;
		}
	}
	& .btn-search{
		background: $primary;
		transition: all .3s;
		&:hover{
			background: darken($primary , 10%);
		}
	}
	& .section-contact.-home03 a{
		&:hover{
			color: $primary;
		}
	}
	& .testimonials.-double .social .fa, 
	& .slider-testimonials.-double .social .fa{
		&:hover{
			color: $primary;
		}
	}
	& .-product-custom{
		& .card-overlay_new{
			background: $primary;
			&:before{
				background: $primary;
			}
		}
	}
	& .card.-product-custom ._body .fa{
		color: $primary;
		&:hover{
			color: darken($primary , 10%);
		}
	}
	& .blog-item .title:after{
		background: $primary;
	}
	& .blog-item ._footer a{
		&:hover{
			color: $primary;
		}
	}
	& .slider-classes-home03 .slider-content ._footer a{
		&:hover{
			color: $primary;
		}
	}
	& .btn{
		&-ico{
			&_active{
				& i {
					color: $primary;
					
				}
			}
		}
	}
	& .section-coaches-single .social a{
		&:hover{
			color: $primary;
		}
	}
	.header.fixed-left .navbar-nav .nav-link.active, .header.fixed-left .navbar-nav .nav-link:hover {
		box-shadow: -4px 0 0 $primary inset;
	}

	.navbar-toggleable-md .navbar-nav .nav-link.active, .navbar-toggleable-md .navbar-nav .nav-link:hover {
		box-shadow: 0 -4px 0 0 $primary inset;
	}

	.lined:before {
		background-color: $primary;
	}

	h1 {
		color: $primary;
	}

	a:focus, a:hover {
		color: $primary;
	}

	.card.-ghost .list-schedule > li:hover .time:after {
		background-color: $primary;
	}

	.slider-content ._header h4 {
		color: $primary;
	}

	.timetable-col.active .timetable-title {
		color: $primary;
	}

	.timetable-wrap.-default .timetable-item:hover {
		border-color: $primary;
	}

	.section-gallery {
		.gallery-caption .naming {
			color: $primary;
		}

		.gallery-caption a:hover, .gallery-caption a:focus {
			color: $primary;
		}
	}

	.card.-product-custom ._body:after {
		box-shadow: 0 -4px 0 $primary inset;
	}

	.section-shop {
		&.-home03 {
			.card.-product-custom ._body .fa {
				color: $primary;

				&:hover,
				&:focus,
				&:focus:active {
					color: darken($primary, 10%);
				}
			}
		}
	}

	.section-testimonials {
		&.-home03 {
			.slider-testimonials.-double .info:before,
			.slider-testimonials.-double .info:after {
				color: $primary;
			}
		}
	}

	.btn-primary.btn-primary {
		color: #000;
		border-color: $primary;
		background-color: $primary;

		&:hover,
		&:focus,
		&:focus:active {
			border-color: $primary;
			color: $primary;
			background: transparent;
		}
	}
	.card .card-footer > a:not(.btn-black):hover{
		color: $primary;
		
	}

	.section-blog {

		&.-home03 {
			.blog-item .title:after {
				background-color: map-get($pallete, home03);
			}
		}
	}
}
@media (max-width: 992px){
	.theme03 {
		& .navbar-toggleable-md .navbar-nav .nav-link.active, 
		& .navbar-toggleable-md .navbar-nav .nav-link:hover{
			background: $primary;
		}
		
	}
}