﻿.slick-lightbox {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100%;
    height: 100%;
    background: black;
    -webkit-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;

}

.slick-lightbox .slick-loading .slick-list {
    background-color: transparent
}

.slick-lightbox .slick-prev {
    left: 15px
}

.slick-lightbox .slick-next {
    right: 15px
}

.slick-lightbox-hide {
    opacity: 0
}

.slick-lightbox-hide.slick-lightbox-ie {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0)
}

.slick-lightbox-hide-init {
    position: absolute;
    top: -9999px;
    opacity: 0
}

.slick-lightbox-hide-init.slick-lightbox-ie {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0)
}

.slick-lightbox-inner {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.slick-lightbox-slick-item {
    text-align: center;
    overflow: hidden
}

.slick-lightbox-slick-item:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.25em
}

.slick-caption-bottom .slick-lightbox-slick-item .slick-lightbox-slick-item .slick-lightbox-slick-caption {
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    width: 100%;
    margin-bottom: 20px
}

.slick-caption-dynamic .slick-lightbox-slick-item .slick-lightbox-slick-item .slick-lightbox-slick-caption {
    display: block;
    text-align: center
}

.slick-lightbox-slick-item-inner {
    display: inline-block;
    vertical-align: middle;
    max-width: 90%;
    max-height: 90%
}

.slick-lightbox-slick-img {
    margin: 0 auto;
    display: block;
    max-width: 90%;
    max-height: 90%
}

.slick-lightbox-slick-caption {
    margin: 10px 0 0;
    color: white
}

.slick-lightbox-close {
    position: absolute;
    top: 0px;
    right: 0px;
    display: block;
    height: 60px;
    width: 60px;
    line-height: 0;
    font-size: 0;
    cursor: pointer;
    background: #616160;
    color: transparent;
    padding: 0;
    transition: all .35s;
    border: none
}

.slick-lightbox-close:focus {
    outline: none
}
.slick-lightbox-close{
	&:hover{
		opacity: 0.8;
		transition: all .35s;
	}
}

.slick-lightbox-close:before {

	transition: all .35s;
    line-height: 60px;
    color: #000;
    opacity: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\f00d";
    display: inline-block;
    font: normal normal normal 25px/1 FontAwesome;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
}
.slick-lightbox-slick{
	& .slick-prev  , & .slick-next{
		background: #616160;
		width: 60px;
		height: 60px;
		transition: all .35s;
		z-index: 99;
		&:before{
	        display: inline-block;
		    font: normal normal normal 14px/1 FontAwesome;
		    font-size: 35px;
			color: #000;
		}
		&:hover{
			opacity: 0.8;
			transition: all .35s;
		}
	}
	& .slick-prev{
		left: 0;
		right: inherit;
		&:before{
		    content: "";
		}
	}
	& .slick-next{
		right: 0;
		left: inherit;
		&:before{
		    content: "";
		}
	}
}	

@media screen and (max-width: 768px){
    .slick-lightbox-slick .slick-prev, .slick-lightbox-slick .slick-next{
        width: 30px;
        height: 30px;
        &:before{
            font-size: 24px;
        }
    }
    .slick-lightbox-close{
        width: 30px;
        height: 30px;
        &:before{
            font-size: 25px;
            line-height: 30px;
        }
    }
}
@media (max-width: 500px){
    .slick-lightbox-inner{
        .slick-next{
            right: 0 !important; 
        }
        .slick-prev{
            left: 0 !important;
        }
    }
}