@font-face {
    font-family: 'LatoSemibold';
    src: url('../fonts/LatoSemibold/LatoSemibold.woff2') format('woff2'),
         url('../fonts/LatoSemibold/LatoSemibold.woff') format('woff');
}

@font-face {
	font-family: 'Gilroy';
	src: url('../fonts/gilroy/Gilroy-Light.eot');
	src: url('../fonts/gilroy/Gilroy-Light.eot?#iefix') format('embedded-opentype'),
		url('../fonts/gilroy/Gilroy-Light.woff') format('woff'),
		url('../fonts/gilroy/Gilroy-Light.ttf') format('truetype');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Gilroy';
	src: url('../fonts/gilroy/Gilroy-ExtraBold.eot');
	src: url('../fonts/gilroy/Gilroy-ExtraBold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/gilroy/Gilroy-ExtraBold.woff') format('woff'),
		url('../fonts/gilroy/Gilroy-ExtraBold.ttf') format('truetype');
	font-weight: 800;
	font-style: normal;
}
