ul, ol {
	list-style: none;
	padding: 0;
	margin: 0;
}

.list-schedule {
	> li {
		padding: 15px 15px 15px 60px;
		transition: background-color .2s;
		cursor: pointer;

		&:hover {
			background-color: #f1f1f1;

			.time {
				&:after {
					background-color: map-get($pallete, home01);
					transform: rotate(135deg);
				}
			}
		}
		& a {
			display: flex;
			width: 100%;
			color: #000;
		}

		.time {
			position: relative;
			flex-basis: 35%;
			font-weight: 700;
			font-size: 18px;
			line-height: 22px;

			&:after {
				content: '';
				display: block;
				width: 10px;
				height: 10px;
				position: absolute;
				top: 10px;
				right: 25%;
				background-color: #000;
				transform: rotate(45deg);
				transition: background-color .2s, transform .2s;
			}
		}

		.action {
			flex-basis: 65%;

			span {
				display: block;
			}
			

			.naming {
				font-weight: 700;
				font-size: 20px;
				line-height: 22px;
				margin-bottom: 5px;
			}

			.who {
				font-size: 18px;
			}
		}
	}

	&.-custom {
		display: flex;
		flex-wrap: wrap;

		> li {
			width: 50%;
			max-width: 50%;
			flex-basis: 50%;
			padding: 5px;

			.time {
				&:after {
					transform: rotate(0);
					width: 7px;
					height: 1px;
				}
			}
		}
	}
}

.list-advantage {
	&.d-flex {
		.item {
			flex-basis: 50%;
			max-width: 50%;
		}
	}

	.item {
		padding: 45px 30px;

		[class^="im-"] {
			font-size: 40px;
			margin-right: 20px;
		}

		._content {
			padding-top: 15px;

			p {
				margin: 0;
			}
		}
	}
}

.list-social {
	> li {
		display: inline-block;

		> a {
			font-size: 24px;
			margin: 0 10px;
			transition: color .2s;

			&:hover {
				text-decoration: none;
			}
		}
	}
}

.list-group {
	> a {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		color: #000;
		font-size: 18px;
		padding: 25px 45px 25px 25px;
		transition: background-color .2s ease-in-out;

		&:before {
			content: "\f0da";
			font: normal normal normal 18px/1 FontAwesome;
			display: block;
			position: absolute;
			top: 50%;
			right: 25px;
			transform: translateY(-50%);
			opacity: 0;
			transition: opacity .2s ease-in-out;
		}

		&:hover,
		&:focus {
			text-decoration: none;
			outline: none;
			background-color: #f1f1f1;

			&:before {
				opacity: 1;
			}
		}

		span {
			flex-basis: 33.33%;
		}

		.type {
			font-weight: bold;
		}

		.price {
			font-weight: bold;
			text-align: right;

			i {
				font-weight: normal;
				margin-right: 5px;
			}
		}
	}
}

.list-contact {
	li {
		display: flex;
		padding-bottom: 15px;

		&:last-child {
			padding-bottom: 0;
		}
	}

	a,
	span {
		font-size: 19px;
		line-height: 1.1;
		display: inline-block;
		vertical-align: middle;
	}

	.fa {
		font-size: 22px;
		width: 25px;
		margin-right: 15px;
		vertical-align: middle;
	}
}

.list-custom {
	font-size: 19px;

	li {
		padding: 13px 0;
		border-bottom: 1px solid #ebebeb;

		span {
			position: relative;
			display: inline-block;
			padding: 0 25px;

			&:before {
				content: '';
				display: block;
				width: 7px;
				height: 7px;
				position: absolute;
				top: 50%;
				left: 0;
				background-color: map-get($pallete, home02);
				opacity: 0;
				transform: translateY(-50%) rotate(45deg);
				transition: opacity .2s ease-in-out;
				pointer-events: none;
			}
		}
	}
}

.list-chip {
	display: flex;
	flex-wrap: wrap;
	background-color: rgba(#000, .26);

	li {
		flex-basis: 25%;
		max-width: 25%;
		height: 275px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		text-align: center;
		border-right: 1px solid rgba(#fff, .2);
		border-bottom: 1px solid rgba(#fff, .2);

		&:nth-child(4n) {
			border-right: none;
		}

		&:nth-last-child(1),
		&:nth-last-child(2),
		&:nth-last-child(3),
		&:nth-last-child(4) {
			border-bottom: none;
		}

		.title {
			font-family: 'Gilroy', sans-serif;
			font-size: 22px;
			font-weight: 900;
			transition: color .2s;
		}

		.small {
			font-size: 18px;
		}

		span {
			display: block;
		}
	}

	.icon-wrap {
		font-size: 60px;
	}
}

.list-default {
	li {
		padding: 30px 0;
		font-size: 20px;
		position: relative;
		padding-right: 15px;
	}

	a {
	    color: #000;
	    position: absolute;
	    right: 0;
	    top: 50%;
	    transform: translateY(-50%);
	    margin: 0;

		&:hover,
		&:focus {
			text-decoration: none;
			outline: none;
		}
	}
}

ol.list-default {
		counter-reset: num;

	li {

		&:before {
			counter-increment: num;
			content: counter(num) ". ";
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;
		}
	}
}

.list-color {
	li {
		display: inline-block;

		a {
			display: block;
			width: 15px;
			height: 15px;
			border-radius: 50%;
		}
	}

	.green {
		background-color: map-get($pallete, green);
	}

	.black {
		background-color: #000;
	}

	.blue {
		background-color: map-get($pallete, blue);
	}
}

.list-location {
	list-style: none;
	counter-reset: num;

	> li {
		margin-bottom: 40px;

		&:last-child {
			margin-bottom: 0;
		}

		.title {
			margin: 0 0 15px;

			&:before {
				counter-increment: num;
				content: counter(num) ". ";
				font-family: 'Gilroy', sans-serif;
				font-weight: 900;
			}
		}
	}
}

.list-rated {
	.img-wrap {
		width: 95px;
		height: 50px;

		img {
			max-height: 100%;
			margin: 0 auto;
		}
	}

	li {
		padding: 15px 0;
	}

	.naming {
		display: block;
		color: #000;
	}
}

.list-inline {
	display: flex;
	flex-wrap: wrap;

	a {
		color: #000;
		transition: color .2s ease-in-out;

		&:hover {
			text-decoration: none;
		}

		&.active {
			font-weight: 700;
		}
	}
}
@media (max-width: 1500px){
	.list-group > a{
		padding: 20px;
		&:before{
			right: 5px;
		}
		& span{
			font-size: 17px;
		}
	}

	.list-schedule{
		& > li {
			padding: 10px 10px 10px 60px;
		}
	}
	.list-advantage {
		& .item{
			padding: 20px;
		}
	}
}
@media (max-width: 1300px){
	.list-chip {
		& li .title{
			font-size: 20px;
		}
	}
	.list-schedule > li .time:after{
		right: 15%;
	}
	.list-rated{
		& .rating > label:before{
			font-size: 15px;
		}
	}
}
@media (max-width: 1100px){

}
@media (max-width: 992px){
	.aside-filter.-inline .list-inline li{
		flex-basis: 25%;
	}
}
@media (max-width: 767px){
	.list-location{
		margin-bottom: 20px;
	}
	.list-chip {
		flex-wrap: wrap;
		& li{
			flex-basis: 50%;
			width: 50%;
			max-width: 50%;
			&:nth-child(2n) {
				border-right: none;
			}
			&:nth-last-child(3),
			&:nth-last-child(4){
				border-bottom: 1px solid rgba(#fff, .2);
			}
		}

	}
}
@media (max-width: 700px){
	.aside-filter.-inline .list-inline li{
		flex-basis: 50%;
		justify-content: center;
    	align-items: center;
	}
}
@media (max-width: 600px){
	.list-advantage.d-flex  {
		& .item{
			flex-basis: 100%;
			padding: 15px 0;

			max-width: 100%;
		}
	}
}
@media (max-width: 500px){
	.list-chip {
		& li{
			flex-basis: 100%;
			width: 100%;
			max-width: 100%;
			border: none;
			border-bottom: 1px solid rgba(#fff, .2);
		}
	}
	.list-group > a{
		display: block;
		&:before{
			display: none;
		}
		& span{
			display: block;
			text-align: center;
			&.price{
				text-align: center;
			}
		}
	}
	.list-schedule{
		& li {
			padding: 10px 10px 10px 25px;
			& .time{
				flex-basis: 40%;
				&:after{
					right: 20px;
				}
			}
		}
	}
}
