.section-testimonials-single {
	margin: 45px;

	a {
		color: #000;
	}

	&.-home04 {
		box-shadow: 0 6px 10px 0 rgba(#000, .14), 0 1px 18px 0 rgba(#000, .12), 0 3px 5px -1px rgba(#000, .3);
		background-color: #202020;
		color: #fff;

		.stories .author {
			color: map-get($pallete, home04);
		}
		a {
			color: #fff;
		}
	}

	&.-outline {
		padding: 0;
		margin: 85px 45px;

		.img-wrap {
			margin: -40px 0;
			& img {
				box-shadow: 0 6px 10px 0 rgba(#000, .14), 0 1px 18px 0 rgba(#000, .12), 0 3px 5px -1px rgba(#000, .3);
				margin: 0 auto;
			}
		}
	}


	.stories {
		.img-wrap {
			border-radius: 0;
		}

		.item {
			margin: 40px 0;
		}
	}

	.color-primary.color-primary {
		color: map-get($pallete, home04);

		a {
			color: map-get($pallete, home04);
			margin-right: 10px;
			font-size: 18px;
			&:last-child{
				margin: 0;
			}
			&:hover{
				color: #fff;
			}
		}
	}
}
.section-testimonials-single.-home04.-outline{
	& .item-content{
		padding: 15px 0;
	}
}
@media (max-width: 1300px){
	.section-testimonials-single.-outline{
		margin: 40px 20px;
	}
	.section-testimonials-single{
		margin:  15px;
	}
}
@media (max-width: 767px){
	.section-testimonials-single.-outline .img-wrap{
		margin: 0;
		margin-top: -40px;
	}
	.section-testimonials-single .stories .item{
		margin: 20px 0;
		& .author{
			width: 100%;
			text-align: center;
		}
		& .social{
			text-align: center;
		}
	}
}