body {
	position: relative;
}
.link{
	font-weight: bold;
	& .fa-caret-right{
		padding-left: 5px;
	}
}

.main {
	padding-top: 80px;
	&.with-scrollspy {
		section {
			padding-top: 80px;
		}
	}

	&.with-aside-header {
		padding-top: 0;
		section {
			padding-left: 125px;
		}

		.full-height {
			height: 100vh;
		}
	}
}

.lined {
	position: relative;
	padding-left: 24px;

	&:before {
		content: '';
		display: block;
		width: 4px;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background-color: #000;
	}

	&.-bot {
		padding-left: 0;
		padding-bottom: 20px;

		&:before {
			width: 40px;
			height: 4px;
			top: auto;
			bottom: 0;

		}
	}
}

.section-classes {
	padding-top: 30px;
	padding-bottom: 30px;
	&.-home-02{
		padding-bottom: 0;
	}

	&.-home-03 {
		position: relative;
		// height: 100vh;
		padding-bottom: 0;

		.slider-content ._footer a .fa {
			color: map-get($pallete, home03);
		}

		.classes-stats-wrapper {
			background-color: #f6f6f6;
		}
	}

	.section-heading {
		padding-bottom: 0;
		margin-bottom: 35px;
	}
}

.classes-stats-wrapper {
	background-color: #f1f1f1;
	padding: 45px 0;
}

.classes-stats {
	display: flex;
	align-items: center;
	justify-content: center;

	.item {
		flex-basis: 25%;
		text-align: center;
		cursor: default;

		span {
			display: block;
		}

	}

	.count {
		font-size: 62px;
		line-height: 1;
		font-weight: 900;
		transition: color .2s ease-in-out;
	}

	.title {
		font-size: 20px;
	}
}

.img-wrap {
	img {
		display: block;
		max-width: 100%;
		height: auto;
		margin: 0 auto;
	}
}

.section-heading {
	position: relative;
	margin-bottom: 35px;

	h2 {
		margin: 0;
	}
}



.nav-simple {
	position: relative;

	.nav {
		a {
			color: #535353;
			font-size: 20px;
			transition: text-shadow .2s;

			&.active {
				color: #000;
				text-shadow: 1px 0 0 #000, 0px 0 0 #000;
			}
			&:hover{
				color: #000;
				text-shadow: 1px 0 0 #000, 0px 0 0 #000;
			}
		}
	}
}

.title-link {
	position: absolute;
	top: 50%;
	right: 0;
	transform: translateY(-50%);

	.link {
		font-weight: 900;
		text-transform: uppercase;
		color: #000;

		.fa {
			margin-left: 10px;
		}
	}
}

.section-price {
	padding: 60px 0;

	&.-home03 {
		padding-top: 0;
		padding-bottom: 0;

		.bg-img01 .section-heading {
			padding: 0;
		}

		.container-fluid {
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			padding-top: 100px;
			padding-bottom: 100px;
		}

		.section-heading {
			margin: 0;
	
			h2 {
				margin: 0 0 20px;
			}

			.subheading {
				max-width: 650px;
				margin: 0 auto;
			}
		}

		.card.-with-img .card-title:after {
			background-color: map-get($pallete, home03);
		}
		.card .level .fa {
			color: map-get($pallete, home03);;
		}
		.card.-with-img .card-header {
			box-shadow: 0 -4px 0 map-get($pallete, home03) inset;
		}
	}
}

.section-testimonials {
	background: no-repeat;
	background-position: center top;
	background-size: cover;
	color: #fff;
	padding: 70px 0;

	&.-home02 {
		background-image: url(../images/section-testimonials-bg-02.jpg);
	}

	&.-home03 {
		background-image: url(../images/bg-img-01.jpg);
	}
	&.-home04 {
		background-image: none;
		background-color: #202020;
		margin: 45px;
		box-shadow: 0 6px 10px 0 rgba(#000, .14), 0 1px 18px 0 rgba(#000, .12), 0 3px 5px -1px rgba(#000, .3);

		.slider-stories.slider-stories-home02 {
			padding: 0 75px;
		}

		.slider-stories .img-wrap {
			width: 190px;
			height: 190px;
			border-radius: 0;
			margin: 0;
			box-shadow: 0 2px 2px 0 rgba(#000, .14), 0 1px 5px 0 rgba(#000, .12), 0 3px 1px -2px rgba(#000, .2);
		}

		.slider-stories.slider-stories-home02 .social a,
		.title-link .link {
			color: #fff;
		}

		.title-link .link {
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;
		}

		.slider-stories.slider-stories-home02 .slick-prev:before,
		.slider-stories.slider-stories-home02 .slick-next:before {
			color: #fff;
		}

		.slider-stories .author {
			text-transform: capitalize;
		}
	}
}

.social {
	a {
		transition: color .3s;

		&:focus,
		&:focus:active,
		&:hover {
			text-decoration: none;
			outline: none;
		}
	}
}


.grid-custom-wrapper {
	margin-left: -10px;
	margin-right: -10px;

	.col {
		padding: 12px 10px;

		&.-quarter {
			width: 25%;
		}

		&.-third {
			width: 33.33%;
		}

		&.-half {
			width: 50%;
		}

		&.-full {
			width: 100%;
		}
	}
}

.section-stories {
	padding: 70px 0;

	&.-home01 {
		color: #fff;
		background: url(../images/section-stories-bg-01.jpg) no-repeat center top / cover;
	}

	.section-heading {
		padding-bottom: 50px;
	}

	&.-home04 {
		margin: 45px;
		background-color: #202020;
		color: #fff;
		box-shadow: 0 6px 10px 0 rgba(#000, .14), 0 1px 18px 0 rgba(#000, .12), 0 3px 5px -1px rgba(#000, .3);

		.section-heading {
			page-break-after: 30px;
		}

		.slick-prev:before, .slick-next:before {
			color: #fff;
		}
	}
}



.short-info {

	h3 {
		font-size: 24px;
		text-transform: uppercase;
		margin: 0 0 20px;
	}

	.title-wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10px 0;
		font-size: 18px;
		line-height: 1.1;
	}

	.title {
		font-size: 22px;
		font-weight: 900;
		color: #000;
		display: block;
	}

	.time {
		font-weight: 700;
	}

	.coach {
		font-size: 18px;
	}

	.item {
		padding: 12px 0;
		margin-bottom: 15px;
	}

	.link {
		color: #000;

		&:hover {
			text-decoration: none;
		}
	}

	&-link {
		text-align: center;

		a {
			font-size: 18px;
			transition: color .2s ease-in-out;

			&:hover {
				text-decoration: none;
				color: map-get($pallete, home01);
			}
		}

		.fa {
			margin-left: 7px;
		}
	}

	&.as-grid {
		margin-left: -30px;
		margin-right: -30px;

		.img-wrap {
			margin-bottom: 12px;
			display: block;
		}

		.item {
			padding: 30px;
		}
	}
}

.bg-img01 {
	background: url(../images/section-stories-bg-01.jpg) no-repeat center top/cover;
	color: #fff;

	.section-heading {
		padding: 40px 0;
		color: #fff;
	}

	.nav {
		.nav-link {
			color: #fff;

			&.active {
				color: #fff;
			}
		}
	}
}

.bg-img02 {
	background: url(../images/section-top-bg-01.jpg) no-repeat center top / cover;
}

.bg-img04 {
	background: url(../images/section-testimonials-bg-01.jpg) no-repeat top center / cover;
	color: #fff;
}

.section-wrapper {
	margin: 45px;
	box-shadow: -12px 1px 15px rgba(0, 0, 0, 0.24), -17px 2px 50px rgba(0, 0, 0, 0.19);
}

.section-advantage {
	&.-home01 {
		.section-heading {
			margin-bottom: 60px;
		}
	}

	&.-home04 {
		background: url(../images/bg-img-03.jpg) no-repeat center center / cover;
		color: #fff;
		padding: 50px 0;
	}
}

.section-location {
	margin: 45px;
	padding: 60px;
	background-color: #202020;
	color: #fff;

	.map-wrapper {
		height: 500px;
	}
}

.section-top-single {
	padding: 120px 0;
	color: #fff;
	h2 {
		margin-bottom: 0;
	}
}



.single-gallery.-home04 {
	margin: 30px;
	box-shadow: 0 4px 5px 0 rgba(#000, .14), 0 1px 10px 0 rgba(#000, .12), 0 2px 4px -1px rgba(#000, .3)
}

.aside-filter {
	&.-inline {
		background-color: #f5f5f5;
		padding: 25px 30px;

		h5 {
			margin: 0 0 15px
		}

		.list-inline {
			li {
				padding: 10px 5px;
				flex-basis: 20%;
				flex-shrink: 0;
				text-align: center;
			}
		}

		.filter-range-wrapper {
			padding: 15px 0;
		}
	}
}

.aside-filter-item {
	& + & {
		margin-top: 40px;
	}
}

.filter-categories {
	background-color: #f1f1f1;
	padding: 25px;
	& > h5 {
		 margin-bottom: 40px;
	}
}

.bgc-white {
	background-color: #fff;
}

.nav{
	align-items: center;
	& a {
		text-align: center;
	}
}
.slider-testimonials , .slider-stories{
	& .slick-prev, & .slick-next{
		width: inherit;
		height: inherit;
	}
}
@media (max-width: 1500px){
	.section-testimonials.-home04 , .section-stories.-home04 , .section-location.-home04 {
		margin:  15px;
	}
	.section-testimonials.-home04 .slider-stories.slider-stories-home02{
		padding: 0 20px;
	}
}
@media (max-width: 1300px){
	.section-wrapper{
		margin: 20px;
	}
	.short-info.as-grid{
		margin: 0 -15px;
	}
	.short-info.as-grid .item{
		padding: 20px;
	}
	.grid-custom-wrapper .col.-quarter{
		width: 33.33%;
	}
	.nav-list{
		& a {
			padding: 10px;
		}
	}

}
@media (max-width: 992px){
	.section-location.-home04{
		padding: 40px 0px;
	}
	.aside-filter.-inline{
		padding: 15px 20px;
	}
	.aside-filter-item.-mob_50{
		display: inline-block;
		width: 48%;
		vertical-align: top;
		&.filter-categories {
			margin-right: 3%;
		}
	}
	.section-testimonials.-home04 , .section-stories.-home04 , .section-location.-home04{
		margin:  0px;
	}
	.section-wrapper{
		margin: 0;
	}
	.main.with-aside-header .full-height{
		height: inherit;
		padding: 90px 0 40px 0;
	}
	.main.with-aside-header section{
		padding-left: 0;
	}
	.grid-custom-wrapper .col.-third{
		width: 50%;
	}
	.classes-stats{
		    align-items: flex-start;
		    & .item{
		    	padding: 0 5px;
		    }
		// flex-wrap: wrap;
		// & .item{
		// 	flex-basis: 50%;
		// }
	}
	.short-info{
		& .coach{
			font-size: 17px;
		}
		& .link{
			align-items: center;
			display: flex;
		}
	}
	.grid-custom-wrapper .col.-quarter{
		width: 50%;
	}
	.section-heading{
		&.-inner_nav.-inner_nav{
			display: block !important; 
			& .nav{
				margin-top: 10px;
				justify-content: center;
			}
		}
	}
}
@media (max-width: 767px){
	.shop-panel{
		margin-top: 20px;
	}
	.aside-filter-item.-mob_50{
		width: 100%;
		&.filter-categories {
			margin-right: 0%;
		}
	}
	.section-top-single{
		padding: 50px 0;
	}
	.grid-custom-wrapper .col.-half{
		width: 100%;
	}
	.classes-stats{
		& .item{
			& .count{
				font-size: 50px;
			}
			// flex-basis: 100%;
			// padding: 5px 0;
		}
	}
	.nav-list{
		flex-wrap: wrap;
			& a {
				flex-basis: 33.33%;
				text-align: center;
			}
	}
	.nav-list.nav-list.nav-list{
		& .nav{
			flex-wrap: wrap;
			& a {
				flex-basis: 33.33%;
				text-align: center;
			}
		}
	}
	.section-heading{
		&.-inner_nav{
			& .nav{
				flex-wrap: wrap;
				& a {
					flex-basis: 33.33%;
					text-align: center;
				}
			}
		}
	}
	.section-advantage.-home01 {
		& h2.lined{
			margin-bottom: 20px;
		}
	}
	.section-heading.section-heading{
		display: block !important;
		& .btn-beige{
			margin-top: 20px;
		}
	}
	.title-link{
		transform: none;
		display: block;
		position: static;
		margin-top: 20px;
	}
}
@media (max-width: 600px){
	.grid-custom-wrapper .col.-third{
		width: 100%;
	}
	.short-info {
		& .title-wrap{
			flex-wrap: wrap;
		}
		& .coach{
			width: 100%;
			margin-bottom: 10px;
		}
		&.as-grid .item{
			text-align: center;
		}
		& .link{
			margin: 0 auto;
		}
	}
}
@media (max-width: 500px){
	.classes-stats{
		flex-wrap: wrap;
		& .item{
				flex-basis: 100%;
		}
	}
	.slider-coaches.-home02 .img-wrap.img-wrap img{
		max-width: 100%;
	}

	.grid-custom-wrapper .col.-quarter{
		width: 100%;
	}
	.nav-list.nav-list.nav-list{
			& a {
				flex-basis: 50%;
			}
	}
	.section-heading{
		&.-inner_nav{
			& .nav{
				& a {
					flex-basis: 50%;
				}
			}
		}
	}
}