.breadcrumb {
	margin: 0;
	background-color: transparent;
	padding: 10px 0;
}

.breadcrumb-item {
    font-weight: 700;
	color: #979797;
	font-size: 16px;
	text-transform: uppercase;
}
@media (max-width: 500px){
	.breadcrumb-item {
		font-size: 13px;
	}
	.breadcrumb-item + .breadcrumb-item::before{
		padding: 0 3px;
		
	}
}