$primary: map-get($pallete, home02);

.theme02 {
	& .modal-price_col_descr{
		border-color: $primary;
	}
	& .short-info .title{
		color: #fff;
	}
	& .btn-search{
		background: $primary;
		transition: all .3s;
		color: #fff;
		&:hover{
			background: darken($primary , 10%);
		}
	}
	& .section-contact .social{
		& a {
			&:hover{
				color: $primary;
			}
		}
	}
	& .search-link .fa:hover{
		color: $primary;
	}
	& .blog-item ._footer a{
		&:hover{
			color: $primary;
		}
	}
	& .blog-item .title{
		&:after{
			background: $primary;
		}
	}
	& .card.-product-wide .link{
		&:hover{
			color: $primary;
		}
	}
	& .slider-coaches .list-social{
		& a {
			transition: all .3s;
			&:hover{
				color: $primary;
			}
		}
	}
	& .btn-beige{
		&:hover{
			background: darken(#f6f6f6 , 10%);
			color: #000;
		}
	}
	& .link{
		&:hover{
			color: $primary;
		}
	}
	h1 {
		color: map-get($pallete, home01);
	}

	.navbar-toggleable-md .navbar-nav .nav-link.active, .navbar-toggleable-md .navbar-nav .nav-link:hover {
		box-shadow: 0 -4px $primary inset;
	}

	.lined:before {
		background-color: $primary;
	}

	.short-info .link .fa,
	.short-info-link .fa {
		color: $primary;
	}

	.section-coaches {
		box-shadow: 0 4px 0 $primary inset;
	}

	.section-timetable {
		.timetable-wrap {
			.timetable-item {
				&:hover {
					box-shadow: 3px 0 0 $primary inset;
				}
			}
		}
	}

	.slider-coaches.-home02 .position {
		color: #21a5e8;
		font-weight: bold;
	}


	.gallery-caption .naming {
		color: $primary;
	}

	.gallery-caption a:hover, .gallery-caption a:focus {
		color: $primary;
	}

	.list-custom li span:before {
		background-color: $primary;
	}

	.slider-stories .social a {
		color: $primary;
		&:hover{
			color: darken($primary , 10%);
		}
	}

	.zoom-link:hover {
		outline-color: $primary;
	}

	.section-shop.-home02 .rating > input:checked ~ label, .section-shop.-home02 .rating:not(:checked) > label:hover, .section-shop.-home02 .rating:not(:checked) > label:hover ~ label {
		color: $primary;
	}

	&.-home-02 {
		padding-bottom: 0;

		.section-heading {
			position: relative;
			margin-bottom: 30px;

			h2 {
				margin: 0;
			}
		}

		.short-info {
			.link {
				color: #000;

				&:hover {
					text-decoration: none;
				}

				.fa {
					color: map-get($pallete, home02);
				}
			}
		}

	}

	.section-blog {
		.blog-item {
			.title {
				&:after {
					background-color: map-get($pallete, home02);
				}
			}
		}
	}
}
@media (max-width: 992px){
	.theme02{
		& .navbar-toggleable-md .navbar-nav .nav-link.active, 
		& .navbar-toggleable-md .navbar-nav .nav-link:hover{
			background: $primary;
		}
	}
}