.gallery-with-gutter {

	.-gutter-wrap {
		margin-left: -10px;
		margin-right: -10px;
	}

	.gallery-item {
		padding: 10px;
	}

	.gallery-caption {
		top: 10px;
		left: 10px;
		bottom: 10px;
		right: 10px;
	}
}
.single-gallery {
	& .gallery-simple-hover {
		& .gallery-caption {
			background: #f5d02c;
			& span {
				color: #000;
			}

		}
	}	
}
.gallery-simple-hover {

	.gallery-caption {
		top: auto;
		bottom: -85px;
		height: 85px;
		opacity: 0;
		visibility: visible;
		background-color: #202020;
		transition: all .3s ease-in-out;

		.naming {
			color: #fff;
			font-family: 'Gilroy', sans-serif;
			font-weight: 900;
		}

		.coach {
			padding: 0;
		}
	}

	.gallery-item  {
		&:hover {
			.gallery-caption {
				bottom: 0;
				transition: all .3s ease-in-out;
			}
		}
	}
}
.theme04{
	& .single-gallery{
		& .gallery-item{
			& .img-wrap{
				padding-bottom: 150%;
			}
		}
		
	}
}
.gallery-item {
	height: auto;
	width: 50%;
	overflow: hidden;

	&.halfheight {
		.img-wrap {
			padding-bottom: 33.35%;
		}
	}

	&.-quarter {
		width: 25%;

		.img-wrap {
			padding-bottom: 100%;
		}
	}

	&.-third {
		width: 33.33%;
	}

	.img-wrap {
		position: relative;
		padding-bottom: 66.7%;

		img {
			position: absolute;
		}
	}

	img {
		display: block;
		width: 100%;
		max-width: none;
		height: 100%;
		object-fit: cover; 
	}

	&:hover {
		.gallery-caption {
			visibility: visible;
			opacity: 1;
		}
	}
}

.gallery-caption {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background-color: rgba(#000, .8);
	opacity: 0;
	visibility: hidden;
	transition: opacity .2s, visibility .2s;

	.naming {
		font-size: 24px;
		font-weight: 900;
		color: #f1f1f1;
	}

	.coach {
		font-size: 20px;
		color: #fff;
		padding-bottom: 30px;
	}

	.fa {
		color: #fff;
		font-size: 20px;
		margin: 0 7px;
	}

	a {
		transition: color .2s ease-in-out;

		&:hover,
		&:focus {
			outline: none;
			text-decoration: none;
			color: #ddd;
		}
	}
}

.section-gallery {
	padding-top: 45px;
	background-color: #f1f1f1;
	&.-home03{
		background: none;
	}

	.section-heading {
		position: relative;
	}

	&.-home04 {
		background-color: #202020;
		color: #fff;
		& .img-wrap{
			padding-bottom: 150%;
		}
		.nav-simple {
			.nav {
				a {
					color: #fff;
					&.active {
						text-shadow: 1px 0 0 #fff, 0px 0 0 #fff;
					}
				}
			}
		}
	}
}

@media (max-width: 1500px){
	.section-gallery{
		& .title-link{
			position: static;
			transform: none;
			text-align: center;
			margin-bottom: 10px;
		}
	}
}
@media (max-width: 992px){
	.section-gallery{
		.gallery-item.halfheight .img-wrap{
			padding-bottom: 66.7%;
		}
	}
	.gallery-item.-quarter{
		width: 33.333%;
	}
}
@media (max-width: 767px){
	.gallery-item.-third{
		width: 50%;
	}
	.gallery-item.-quarter{
		width: 50%;
	}
	.gallery-caption{
		& .coach{
			padding-bottom: 15px;
		}
	}
}
@media (max-width: 500px){
	.single-gallery.-home04{
		margin: 15px;
	}
	.theme04 .single-gallery .gallery-item .img-wrap{
		padding-bottom: 100%;
	}
	.section-gallery {
			&.-home04 {
				& .img-wrap{
					padding-bottom: 100%;
				}
			}
		}
	.gallery-item  , .gallery-item.-quarter , .gallery-item.-third{
		width: 100%;
	}
}