.slider-classes-for {
	padding: 40px 60px;

	.item {
		display: flex;
		align-items: center;

		.img-wrap {
			flex-basis: 50%;
			display: block;
		}

		.slider-content {
			flex-basis: 50%;
			& ._header{
				& a {
					font-size: 2rem;
					text-transform: uppercase;
					font-weight: 900;
					color: #000;
				}
				
			}
		}
	}

	.slider-content {
		padding-left: 60px;
	}
}

.slider-classes-nav {
	padding: 0 45px;

	.item {
		margin: 0 15px;

		h4 {
			font-size: 22px;
			text-transform: capitalize;
		}
	}

	.img-wrap {
		margin-bottom: 10px;
	}
}

.slider-content {
	._header {
		padding-bottom: 20px;
		& a {
			font-size: 2rem;
			text-transform: uppercase;
			font-weight: 900;
			color: #000;
		}

		h4 {
			font-size: 28px;
			color: #000;
			margin: 0;
		}

		.subtitle {
			display: block;
			font-size: 20px;
			font-weight: 700;
		}
	}

	._body {
		font-size: 16px;

		p {
			&:last-child {
				// margin-bottom: 0;
			}
		}
	}
}

.slider-coaches {
	padding: 0 60px;

	.item {
		color: #fff;

		.img-wrap {
			flex-basis: 36%;

			img {
				max-width: none;
				max-height: 100%;
				width: auto;
				max-width: 240px;
			}
		}
		.slider-item-content {
			flex-basis: 64%;
			padding-left: 30px;
			padding-right: 50px;

			._header {
				padding-bottom: 20px;
				& .position{
					font-weight: bold;
					font-size: 20px;
				}
			}
		}
	}

	.h2 {
		color: #f1f1f1;
		margin: 0;
		display: block;
		text-transform: uppercase;
	}

	.list-social {
		padding-left: 20px;

		a {
			color: #fff;

			&:hover {
				color: map-get($pallete, home01);
			}
		}
	}

	.slick-prev,
	.slick-next {
		&:before {
			color: #fff;
		}
	}

	&.-home02 {
		.h2 {
			color: #fff;
		}

		.item {
			.slider-item-content {
				padding: 0;

				._header {
					padding: 0 0 7px;
				}
			}
		}

		.img-wrap {
			img {
				height: 390px;
				width: auto;
				margin: 0 auto;
			}
		}
	}
}

.slider-price {
	padding: 0 7%;

	.item {
		padding: 0 20px;
	}

	.slick-prev {
		left: 30px
	}

	.slick-next {
		right: 30px;
	}
}

.testimonials,
.slider-testimonials {
	.item {
		margin: 0 75px;
	}

	.title {
		// font-size: 24px;
		color: map-get($pallete, home01);
	}

	.author-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.author {
		display: flex;
		align-items: center;

		.name {
			font-size: 24px;
			font-weight: 900;
		}
	}

	.author-img {
		overflow: hidden;
		width: 65px;
		height: 65px;
		border-radius: 50%;
		margin-right: 12px;

		img {
			height: 100%;
			max-width: none;
		}
	}

	.slick-prev,
	.slick-next {
		&:before {
			color: #fff;
		}
	}

	.social {
		a {
			display: inline-block;
			color: #fff;
			font-size: 18px;
			margin: 0 10px;
			padding: 10px;

			&:hover {
				color: map-get($pallete, home01);
			}
		}
	}

	&.-double {
		padding: 0 30px;

		.item {
			display: flex;
			align-items: flex-start;
			margin: 0;
			padding: 40px 60px;
		}

		.author-img {
			flex-basis: 65px;
			flex-shrink: 0;
		}

		.name {
			font-size: 23px;
			font-weight: 900;
		}

		.description {
			padding-left: 30px;
		}

		.info {
			margin: 0;
			font-size: 20px;
			line-height: 1.4;

			&:before {
				content: '“';
				display: inline-block;
				padding-right: 10px;
			}

			&:after {
				content: '”';
				display: inline-block;
				padding-left: 10px;
			}
		}

		.social {
			padding-left: 20px;

			.fa {
				margin: 0;
			}
		}
	}
}

.stories,
.slider-stories {
	&-home01 {
		padding: 0 10%;
	}

	.item {
		display: flex;
		justify-content: space-around;
		align-items: flex-start;
	}

	.slick-prev,
	.slick-next {
		&:before {
			color: #fff;
		}
	}

	.img-wrap {
		overflow: hidden;
		height: 196px;
		width: 196px;
		border-radius: 50%;
		margin: 0 auto;
		margin-bottom: 10px;
	}

	.author {
		display: inline-block;
		font-size: 24px;
		font-weight: 900;
	}

	.social {
		a {
			margin: 0 10px;
			font-size: 18px;

			&:hover,
			&:focus,
			&:focus:active {
				color: #fff;
			}
		}
	}

	.item-aside {
		text-align: center;
		padding: 0 30px;
		flex-basis: 30%;
	}

	.item-content {
		padding: 0 30px;
		flex-basis: 70%;
	}

	.title {
		margin-bottom: 15px;
	}

	.description {
		font-size: 18px;
		padding-bottom: 20px;

		p {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.block-zoom-wrapper {
		max-width: 450px;
		margin: 0 auto;
		padding-bottom: 50px;
	}

	&.slider-stories-home02 {
		padding: 0 45px;

		.img-wrap {
			width: 135px;
			height: 135px;
			margin: 0 auto;
		}

		.slick-slide {
			margin: 0 30px;
		}

		.item-aside {
			padding-left: 0;
			padding-right: 20px;
		}
		& .social{
			& a {
				&:hover{
					// color: darken($primary , 20%);
				}
			}
		}
		.item-content {
			padding-right: 0;
			padding-left: 20px;
		}

		.slick-prev,
		.slick-next {
			&:before {
				color: #000;
			}
		}
	}
}

.slider-quotes {
	&.-nav {
		.item {
			margin: 0 15px;
		}
	}

	&.-for {
		padding: 0 60px;

		.slick-prev,
		.slick-next {
			&:before {
				color: #fff;
			}
		}
	}

	.item {
		.img-wrap {
			overflow: hidden;
			width: 146px;
			height: 146px;
			margin: 0 auto;
			border-radius: 50%;
			border: 3px solid map-get($pallete, blue);
			transform: scale(.75);
			transition: transform .2s;
		}

		.name {
			display: block;
			font-size: 24px;
			font-weight: 900;
			text-align: center;
			opacity: 0;
			visibility: hidden;
			transition: opacity .2s, visibility .2s;
		}

		.social {
			text-align: center;
			opacity: 0;
			visibility: hidden;
			transition: opacity .2s, visibility .2s;

			a {
				color: #fff;
				margin: 0 7px;
			}
		}

		&.slick-current {
			.img-wrap {
				transform: scale(1);
			}

			.name,
			.social {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	.quote {
		position: relative;
		text-align: center;
		font-size: 20px;
		margin: 15px 0 0;
		padding: 25px;

		&:before,
		&:after {
			content: "\f10d";
			font: normal normal normal 20px/1 FontAwesome;
			color: map-get($pallete, blue);
			position: absolute;
			top: 0;
			left: 0;
		}
		&:before {
			content: "\f10e";
			top: auto;
			left: auto;
			right: 0;
			bottom: 0;
		}
	}
}

.slider-coaches-home04 {

	.item {
		display: flex;
		align-items: center;
		background-color: #202020;
		color: #fff;
		padding: 20px 250px;
		margin: 60px 0;
	}

	.slider-content {
		padding-left: 60px;
	}

	.img-wrap {
		flex-basis: 50%;
		flex-shrink: 0;
		margin: -60px 0;
		position: relative;
	}

	.slick-prev,
	.slick-next {
		z-index: 1;
		left: 120px;

		&:before {
			color: #fff;
		}
	}

	.slick-next {
		left: auto;
		right: 120px;
	}

	.social a {
		color: map-get($pallete, home04);
		margin: 0 15px 0 0;
	}
}

.slider-stories-home04 {
	padding: 0 75px;

	.item {
		display: flex;
	}

	.item-half {
		flex-basis: 50%;
		flex-shrink: 0;
	}

	.img-wrap {
		width: 155px;
		height: 155px;
	}

	.info {
		padding-left: 30px;
	}

	.author {
		font-family: 'Gilroy', sans-serif;
		font-weight: 900;
		color: map-get($pallete, home04);
		font-size: 28px;
		margin: 0;
	}

	.quote {
		position: relative;
		margin: 0;
		padding: 20px 10px;
		font-family: 'Gilroy', sans-serif;
		font-weight: 900;
		font-size: 17px;
		text-transform: uppercase;

		&:before,
		&:after {
			display: inline-block;
			color: map-get($pallete, home04);
		}

		&:before {
			content: '“';
			padding-right: 10px;
		}

		&:after {
			content: '”';
			padding-left: 10px;
		}
	}

	.social {
		a {
			color: map-get($pallete, home04);
			margin-right: 10px;
		}
	}

	.description {
		padding: 30px 0;

		p {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.img {
		height: 175px;
		flex-basis: 30%;
		flex-shrink: 0;
		margin: 10px;
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;

		&.-wide {
			flex-basis: 60%;
		}
	}
}

.slider-classes-home03 {
	padding: 0 45px;
	& .slick-track{
		padding-bottom: 20px;
	}
	.item.item.item {
		padding: 30px 10px;
		margin: 0 15px;
		transition: all .3s;

		&:hover {
			box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
		}
	}

	.img-wrap {
		display: block;
		width: 100%;
		height: 285px;
		margin-bottom: 25px;
		background-repeat: no-repeat;
		background-size: auto 100%;
		background-position: center center;
	}
	
	.slider-content {
		._header {
			padding-bottom: 10px;

			h4 {
				font-size: 22px;
				color: #000;
				text-transform: none;
			}

			.subtitle {
				font-size: 18px;
			}
		}


		._body {
			height: 65px;
			overflow: hidden;
			margin-bottom: 20px;

			p:last-child {
				margin: 0;
			}

			p {
				line-height: 1.9;
			}
		}

		._footer {
			a {
				color: #000;
				font-weight: 700;

				&:hover {
					text-decoration: none;
				}

				.fa {
					margin-left: 7px;
				}
			}
		}
	}

	&.margin-item-0 {
		padding: 0 0 30px;

		.item.item.item {
			margin: 0;
			padding: 20px;

			transition: all .3s;

			&:hover {
				box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.11);
			}
		}

		p {
			line-height: 1.9;
		}
	}
}

.slider-single-shop {
	display: flex;

	.slider-aside {
		flex-basis: 155px;
		flex-shrink: 0;
		padding-right: 15px;
		& img {
			cursor: pointer;
		}
	}

	.slider-content {
		
		max-width: calc(100% - 155px);
	}

	img {
		max-width: 100%;
		display: block;
		height: auto;
	}
}

	 .slick-prev,  .slick-next{
		width: inherit;
		height: inherit;
	}

@media (max-width: 1500px){
	.slider-coaches-home04 {
		& .item{
			padding: 20px 150px;
		}
		& .slider-content{
			padding-left: 30px;
		}
	}
	.slider-classes-for .item .slider-content{
		padding-left: 30px;
	}
	.stories.slider-stories-home02, .slider-stories.slider-stories-home02 , .slider-stories-home04{
		padding: 0 25px;
	}
	.stories-home01, .slider-stories-home01 , .slider-classes-home03{
		padding: 0 30px;
	}
	.slider-coaches .item .slider-item-content{
		padding: 10px 0px 10px 25px;
	}
	.slider-price{
		padding: 0 30px;
		& .slick-prev, &  .slick-next{
			width: inherit;
			height: inherit;
		}
	}
	.testimonials.-double .item, .slider-testimonials.-double .item{
		padding: 20px 30px;
	}
}
@media (max-width: 1300px){
	.slider-coaches .item {
		display: block !important;
		& .slider-item-content ._header{
			text-align: center;
		}
	}
	.slider-stories-home04 .img.-wide{
		flex-basis: 50%;
	}
	.stories .item-aside, .slider-stories .item-aside ,
	.stories .item-content, .slider-stories .item-content{
		padding: 0 15px; 
	}
}

@media (max-width: 992px){
	.slider-single-shop{
		margin-bottom: 20px;
		display: block;
	}
	.slider-single-shop .slider-content{
		max-width: 100%;
	}
	.slider-stories-home04 .item{
		display: block;
		&-half.d-flex.flex-wrap{
			display: none !important;
		}
	}
	.slider-coaches-home04 {
		& .item{
			display: block;
			margin: 0;
			padding: 20px 30px;
		}
		& .img-wrap{
			margin: 0;
			padding-bottom: 20px;
		}
		& .slick-next{
			right: 0;
		}
		& .slick-prev{
			left: 0;
		}
		& .slider-content{
			padding-left: 0;
		}
	}
	.testimonials.-double .item, .slider-testimonials.-double .item{
		padding: 20px 0;
	}
	.stories .item-content, .slider-stories .item-content , .stories .item-aside, .slider-stories .item-aside{
		padding: 0 10px;
	}
	.testimonials .item, .slider-testimonials .item{
		margin: 0 50px;
	}
	.slider-price{
		padding: 0 20px;
		& .item{
			padding: 0 10px;
		}
		& .slick-prev{
			left: 0;
		}
		& .slick-next{
			right: 0;
		}
	}
	.slider-coaches .item .slider-item-content{
		padding: 10px 0;
	}
	.slider-classes-for .item{
		display: block;
		& .img-wrap{
			margin-bottom: 20px;
		}
		& .slider-content{
			padding: 0;
		}
	}
	.slider-classes-for{
		padding: 40px 30px;
		& .slick-prev, & .slick-next{
			width: inherit;
			height: inherit;
		}
	}
}
@media (max-width: 767px){
	.stories .block-zoom-wrapper, .slider-stories .block-zoom-wrapper{
		margin: 0 auto;
	}
	.stories .item, .slider-stories .item.item{
		display: block;
		& .item-aside{
			margin-bottom: 15px;
			padding-right: 0;
